import React, { Component } from "react";
import styles from "./LogTerminal.module.scss";
import { InMemoryLogProvider } from "../../services/providers/InMemoryLogProvider";
import LoggingService from "../../services/LoggingService";

class LogTerminal extends Component {
    state = {
        isVisible: false,
        logData: "Nothing logged yet...",
        dataCopied: false
    };

    logDataTextarea = React.createRef();

    componentDidMount() {
        window.addEventListener("keydown", this.handleKeyDown);
    }

    componentDidUpdate(_, prevState) {
        let provider = LoggingService.getLogProvider();
        let log = "";

        if (provider instanceof InMemoryLogProvider) {
            log = provider.logs.reduce((p, c) => `${p}\n${c.message}`, '');
            log = log.substr(1, log.length - 1);
        }
        else {
            log = "This is a web version";
        }

        const { logData, dataCopied } = this.state;

        if (logData !== log) {
            this.setState({ logData: log });
        }

        if (prevState.dataCopied !== dataCopied && dataCopied) {
            setTimeout(() => this.setState({ dataCopied: false }), 1500);
        }
    }

    componentWillUnmount() {
        window.removeEventListener("keydown", this.handleKeyDown);
    }

    handleKeyDown = e => {
        if (e.ctrlKey && e.keyCode === 65) {
            e.preventDefault();

            if (!this.state.isVisible) {
                this.setState({ isVisible: true });
            }
        }
    };

    handleBackgroundClick = e => {
        if (e.target === e.currentTarget) {
            this.setState({ isVisible: false });
        }
    };

    hidePanel = () => {
        this.setState({ isVisible: false });
    };

    copyToClipboard = e => {
        e.preventDefault();
        this.logDataTextarea.current.select();
        document.execCommand("copy");
        this.setState({ dataCopied: true });
    };

    render() {
        const { logData, dataCopied } = this.state;

        return (
            this.state.isVisible && (
                <div className={styles.wrapper} onClick={this.handleBackgroundClick}>
                    <section className={styles.innerContent}>
                        <header className={styles.heading}>
                            <h2>Device Information</h2>

                            <button className={styles.closeIcon} onClick={this.hidePanel}>
                                <i className="ms-Icon ms-Icon--ChromeClose"></i>
                            </button>
                        </header>

                        <textarea
                            className={styles.logDataContainer}
                            ref={this.logDataTextarea}
                            value={logData}
                            readOnly
                        />

                        <button onClick={this.copyToClipboard}>
                            <span>
                                <i
                                    className={`ms-Icon ${dataCopied ? "ms-Icon--Completed" : "ms-Icon--Copy"}`}
                                />
                                {dataCopied ? "Data copied" : "Copy to clipboard"}
                            </span>
                        </button>
                    </section>
                </div>
            )
        );
    }
}

export default LogTerminal;
