import React, { forwardRef } from "react";
import PropTypes from "prop-types";
import HelpMeChooseService from "../../../../../../services/HelpMeChooseService";
import CSSService from "../../../../../../services/CSSService";
import styles from "./Navigation.module.scss";

const slideOffset = 250;
const navOptions = { NEXT: "NEXT", PREV: "PREV" };

const setSlideOffset = direction => {
    const offset = direction === navOptions.NEXT ? 1 : -1;
    CSSService.setCustomProperty("fadeSlideOffset", `${slideOffset * offset}px`);
};

const Navigation = forwardRef(
    ({ setCurrentQuestion, setShowResults, setShowLoading, delay }, ref) => {
        const canGoBack = HelpMeChooseService.canGoBack;
        const canGoForward = HelpMeChooseService.canGoForward;

        const checkIfCanShowResults = direction => {
            if (direction === navOptions.NEXT && HelpMeChooseService.isLastQuestion) {
                setShowResults(true);
                setShowLoading(true);
                setTimeout(() => setShowLoading(false), delay);
                return;
            }
        };

        const navigate = direction => {
            checkIfCanShowResults(direction);
            setSlideOffset(direction);

            // eslint-disable-next-line default-case
            switch (direction) {
                case navOptions.NEXT:
                    setCurrentQuestion(HelpMeChooseService.goForward());
                    break;
                case navOptions.PREV:
                    setCurrentQuestion(HelpMeChooseService.goBack());
                    break;
            }
        };

        return (
            <div {...{ ref }} className={styles.navButtons}>
                {canGoBack && (
                    <button onClick={() => navigate(navOptions.PREV)}>
                        <span className={styles.buttonIcon}>
                            <i className="ms-Icon ms-Icon--ChevronLeft" />
                        </span>

                        <span>Prev</span>
                    </button>
                )}

                {canGoForward && (
                    <button onClick={() => navigate(navOptions.NEXT)}>
                        <span className={styles.buttonIcon}>
                            <i className="ms-Icon ms-Icon--ChevronRight" />
                        </span>

                        <span>Next</span>
                    </button>
                )}
            </div>
        );
    }
);

Navigation.propTypes = {
    setCurrentQuestion: PropTypes.func.isRequired,
    setShowResults: PropTypes.func.isRequired,
    setShowLoading: PropTypes.func.isRequired,
    delay: PropTypes.number.isRequired
};

export default Navigation;
