export class Translator
{
    applyLanguage(model, language, prefix)
    {
        for (var key in language)
        {
            if (language.hasOwnProperty(key) && key.startsWith(prefix))
            {
                this.applyValue(model, key, language[key]);
            }
        }
    }

    applyValue(model, path, value)
    {
        let pathProperties = path.split(".");
        let object = model;
        let oldObject = null;
        let oldProperty = null;
        let property = "";

        for (var i = 1; i < pathProperties.length - 1; i++)
        {
            property = pathProperties[i];

            if (oldObject && oldProperty && typeof oldObject[oldProperty] === "string" && !isNaN(property))
            {
                object = oldObject[oldProperty] = [];
            }

            if (object.hasOwnProperty(property))
            {
                oldObject = object;
                oldProperty = property;
                object = object[property];
            }
            else
            {
                console.log(`The model does not have the property ${path}`)
            }
        }

        property = pathProperties[pathProperties.length - 1];

        if (oldObject && oldProperty && typeof oldObject[oldProperty] === "string" && !isNaN(property))
        {
            object = oldObject[oldProperty] = [];
        }
        if (object.hasOwnProperty(property))
        {
            object[property] = value;
        }
        else
        {
            console.log(`The model does not have the property ${path}`)
        }
    }
}