import { CacheService } from "../CacheService";
import LoggingService from "../LoggingService";
import { ScreenResolution } from "../../models/ScreenResolution";

export class PreloadProvider {
    /**
     * Execute the slides resources preload process.
     * @param {Array} slidesResources the slides resources.
     * @param {function} afterLoadResourceCallback the function to call after load resources.
     */
    async processResources(slidesResources, afterLoadResourceCallback) {
        let slidesResourcesToReload = [];

        const { preloadConfig } = window.carouselApp;
        const cacheService = new CacheService('cache', 25);
        const screenResolution = this.getScreenResolution();
        const cacheFunction = async (object, useScreenResolution, index) =>
        {
            const resourceKeys = Object.keys(object.resource)
                .filter(key => preloadConfig.resourceKeysToProcess.includes(key) ||
                               preloadConfig.resourcesToSearch.includes(key));

            let resourceValues = resourceKeys.map(key => object.resource[key]);

            if (useScreenResolution)
            {
                const resourceToReload = slidesResourcesToReload[index];

                // reload the resource original value
                if (resourceToReload)
                    resourceValues = resourceToReload.resourceValues;
            }
            else
            {
                // keep the original resource value to download with the correct resolution
                slidesResourcesToReload.push({ resourceValues });
            }

            const cachedResources = await cacheService.downloadFileList(resourceValues, useScreenResolution ? screenResolution : null, true, true);
            cachedResources.forEach((blobUrl, i) => object.resource[resourceKeys[i]] = blobUrl);

            if (afterLoadResourceCallback)
                afterLoadResourceCallback();
        };

        LoggingService.log('Started placeholders download');
        // wait for the placeholders to download
        await Promise.all(slidesResources.filter(x => x.priority < 10).map(x => cacheFunction(x, false)));
        LoggingService.log('Finished placeholders download');

        // download in background the resources with the correct resolution
        Promise.all(slidesResources.map((x, i) => cacheFunction(x, true, i)));
    }

    /**
     * Gets the current screen resolution.
     */
    getScreenResolution()
    {
        const screenWidth = window.screen.width;

        return screenWidth >= 3840
            ? ScreenResolution.UHDV
            : screenWidth >= 2048 ? ScreenResolution.FULLHD : ScreenResolution.HD;
    }
}