import React, { useRef, useState, useEffect } from "react";
import PropTypes from "prop-types";
import CSSService from "../../../../../../services/CSSService";
import useEventListener from "../../../../../../hooks/useEventListener";
import ScrollMessage from "../../ScrollMessage";
import styles from "./Desktop.module.scss";

const SWIPE_THRESHOLD = 10;

const Desktop = ({
    devices,
    leftDevice,
    rightDevice,
    comparisonTable,
    showMore,
    handleClose,
    toggleShowMore
}) => {
    const [isEndOfScroll, setIsEndOfScroll] = useState(false);
    const scrollableArea = useRef();
    const specsTable = useRef();

    const setTableHeight = () => {
        CSSService.setCustomProperty(
            "tableHeight",
            `${specsTable?.current?.clientHeight}px`,
            specsTable.current
        );
    };

    const checkIfEndOfScroll = ({ scrollHeight, scrollTop, clientHeight }) => {
        setIsEndOfScroll(scrollHeight - scrollTop <= clientHeight + SWIPE_THRESHOLD);
    };

    useEffect(() => {
        setTableHeight();

        if (showMore) checkIfEndOfScroll(scrollableArea.current);
        else setIsEndOfScroll(false);
    }, [leftDevice, rightDevice, showMore]);

    useEventListener("resize", () => {
        setTableHeight();
        checkIfEndOfScroll(scrollableArea.current);
    });

    return (
        <div
            className={`${styles.comparisonTable} ${showMore && !isEndOfScroll ? styles.hasOverlay : ""}`}
        >
            <div ref={scrollableArea} onScroll={({ target }) => checkIfEndOfScroll(target)}>
                <button className={styles.tableBtn} onClick={handleClose}>
                    <span>
                        <i className="ms-Icon ms-Icon--ChromeClose" />
                    </span>
                    {comparisonTable.closeBtn}
                </button>

                <div ref={specsTable} className={styles.specsSection}>
                    <div className={styles.tableRow}>
                        <h2>{devices[leftDevice].name}</h2>
                        <div />
                        <h2>{devices[rightDevice].name}</h2>
                    </div>

                    {comparisonTable.specsToShow.map(
                        (spec, i) =>
                            i <= (showMore ? comparisonTable.specsToShow.length - 1 : 2) && (
                                <div className={styles.tableRow} key={spec.id}>
                                    <p>{devices[leftDevice].specs[i].paragraph}</p>
                                    <div>
                                        <span>{spec.name}</span>
                                    </div>
                                    <p>{devices[rightDevice].specs[i].paragraph}</p>
                                </div>
                            )
                    )}
                </div>

                <button className={styles.tableBtn} onClick={toggleShowMore}>
                    <span>
                        <i
                            className={`ms-Icon ms-Icon--${
                                showMore ? "CalculatorSubtract" : "Add"
                            }`}
                        />
                    </span>
                    {showMore ? comparisonTable.lessSpecs : comparisonTable.moreSpecs}
                </button>
            </div>

            {showMore && !isEndOfScroll && <ScrollMessage />}
        </div>
    );
};

Desktop.propTypes = {
    devices: PropTypes.array.isRequired,
    leftDevice: PropTypes.number.isRequired,
    rightDevice: PropTypes.number.isRequired,
    comparisonTable: PropTypes.object.isRequired,
    showMore: PropTypes.bool.isRequired,
    handleClose: PropTypes.func.isRequired,
    toggleShowMore: PropTypes.func.isRequired
};

export default Desktop;
