import { PlatformDetectionService } from "./PlatformDetectionService";
import LoggingService from "./LoggingService";

class ResourcePreloadService
{
    /**
     * Set the preload provider.
     * @param {Object} logProvider the preload provider.
     */
    setPreloadProvider(preloadProvider)
    {
        this.preloadProvider = preloadProvider;
    }

    /**
     * Preload the slides resources.
     * @param {Object} slides the slides object.
     * @param {string} initialSlideId the initial slide id.
     * @param {function} afterLoadResourceCallback the function to call after load resources.
     */
    async preloadSlidesResources(slides, initialSlideId, afterLoadResourceCallback)
    {
        if (!PlatformDetectionService.isDesktop())
        {
            LoggingService.log('exit preload because device is not desktop');
            return;
        }

        if (!this.preloadProvider)
        {
            console.warn('preloadProvider was not initialized');
            return;
        }

        const { preloadConfig } = window.carouselApp;

        if (!preloadConfig)
        {
            LoggingService.warn('preload configuration not found');
            return;
        }

        const slidesKeys = Object.keys(slides);
        const initialSlideIndex = slidesKeys.findIndex(x => x === initialSlideId);
        let slidesResources = [];

        slidesKeys.forEach((key, i) =>
        {
            const slide = slides[key];
            const slideResources = this.getResourcesFromObject(slide, preloadConfig);
            const slidePriority = i - initialSlideIndex;
            slideResources.forEach(x => x.priority += slidePriority >= 0 ? slidePriority : (slidesKeys.length + slidePriority));
            slidesResources = slidesResources.concat(slideResources);
        });

        slidesResources = slidesResources.sort((a, b) => a.priority - b.priority);

        await this.preloadProvider.processResources(slidesResources, afterLoadResourceCallback);
    }

    /**
     * Gets the resources paths contained in an object.
     * @param {Object} object the object.
     * @param {Object} preloadConfig the preload configuration.
     */
    getResourcesFromObject(object, preloadConfig)
    {
        let resources = [];

        for (var key in object)
        {
            if (object.hasOwnProperty(key))
            {
                const keyIncludedInSearch = preloadConfig.resourcesToSearch.includes(key);
                const keyValue = object[key];

                if (this.hasProperties(keyValue))
                {
                    if (keyIncludedInSearch)
                    {
                        resources.push(
                            {
                                priority: this.getPriorityValue(keyValue, preloadConfig.resourcesWeight),
                                resource: keyValue
                            });
                    }
                    else
                    {
                        resources = resources.concat(this.getResourcesFromObject(keyValue, preloadConfig));
                    }
                }
                else if (keyIncludedInSearch && typeof keyValue === "string")
                {
                    resources.push(
                        {
                            priority: this.getPriorityValue(keyValue, preloadConfig.resourcesWeight),
                            resource: object //store the entire object to keep reference to model
                        });
                }
            }
        }

        return resources;
    }

    /**
     * Get if the provided object has properties.
     * @param {Object} object the object.
     */
    hasProperties(object)
    {
        if (typeof object === "object")
        {
            for (var key in object)
            {
                if (object.hasOwnProperty(key))
                    return true;
            }
        }

        return false;
    }

    /**
     * Gets the priority weight based on object properties value.
     * @param {Object} object the object.
     * @param {Array} resourcesWeight the resources weight.
     */
    getPriorityValue(object, resourcesWeight)
    {
        const findWeight = value =>
        {
            const resourceWeight = resourcesWeight.find(x => value.toLowerCase().includes(x.text));
            return resourceWeight ? resourceWeight.weight : 0;
        };

        if (typeof object === "object")
        {
            for (var key in object)
            {
                if (object.hasOwnProperty(key))
                {
                    const keyValue = object[key];

                    if (keyValue)
                        return findWeight(keyValue);
                }
            }
        }

        return typeof object === "string" ? findWeight(object) : 0;
    }
}

export default new ResourcePreloadService();