export class LegacyConfigurationService {
    static getBoolean(key) {
        if (window.SDX && window.SDX.legacyConfiguration)
            return window.SDX.legacyConfiguration.getBoolean(key);
    }

    static getString(key) {
        if (window.SDX && window.SDX.legacyConfiguration)
            return window.SDX.legacyConfiguration.getString(key);
    }

    static getDouble(key) {
        if (window.SDX && window.SDX.legacyConfiguration)
            return window.SDX.legacyConfiguration.getDouble(key);
    }
}
