import React, { useRef, useState, useEffect } from "react";
import PropTypes from "prop-types";
import HelpMeChooseService from "../../../../../services/HelpMeChooseService";
import CSSService from "../../../../../services/CSSService";
import styles from "./Loading.module.scss";

const Loading = ({ data, delay }) => {
    const loadingRef = useRef();
    const selected = HelpMeChooseService.getFinalSelections();
    const [current, setCurrent] = useState(0);
    const { id, image, icon, text } = selected[current];

    if (current < selected.length - 1)
        setTimeout(() => setCurrent(current + 1), delay / selected.length);

    useEffect(() => {
        CSSService.setCustomProperty(
            "loadingDuration",
            `${delay / selected.length}ms`,
            loadingRef.current
        );
    }, [delay, selected.length]);

    return (
        <div ref={loadingRef} className={styles.loading}>
            <h1>{data.title}</h1>

            <div key={id} className={styles.loadingOption}>
                <div className={styles.imageContainer}>
                    <img src={icon ? icon : image} alt={`${text} icon`} />
                </div>

                <h3>{text}</h3>
            </div>

            <div className={styles.loadingProgress} />
        </div>
    );
};

Loading.propTypes = {
    data: PropTypes.object.isRequired,
    delay: PropTypes.number.isRequired
};

export default Loading;
