import React, { useContext, useState, useEffect, useCallback } from "react";
import PropTypes from "prop-types";
import { CSSTransition } from "react-transition-group";
import HelpMeChooseService from "../../../services/HelpMeChooseService";
import TelemetryService from "../../../services/TelemetryService";
import { CarouselContext } from "../../../contexts/CarouselContext";
import CSSService from "../../../services/CSSService";
import Heading from "../../SlideBlocks/Heading";
import OptionsWrapper from "./HelpMeChooseSection/QuestionsSwitch/Question/OptionsWrapper";
import OptionCard from "../../SlideBlocks/OptionCard";
import HelpMeChooseSection from "./HelpMeChooseSection";
import CompareSection from "./CompareSection";
import styles from "./SelectionSlide.module.scss";

const queryParamsId = {
    section: "section",
    result: "result",
    devices: {
        left: "left",
        right: "right"
    }
};
const transitionDelay = 500;
CSSService.setCustomProperty("initialSelectionDelay", `${transitionDelay}ms`);

const SelectionSlide = ({ slide }) => {
    const queryParams = new URLSearchParams(window.location.search);
    const { setCanSwipe } = useContext(CarouselContext);
    const [selected, setSelected] = useState(null);
    const [queriedResult, setQueriedResult] = useState(null);
    const [selectedSection, setSelectedSection] = useState({ current: null, in: false });
    const [queriedDevices, setQueriedDevices] = useState({ left: null, right: null });

    const setSection = section => {
        if (selected) return;

        setSelected(section);

        setTimeout(() => {
            if (section.id === "help-me-choose") HelpMeChooseService.initialize();
            setSelectedSection({ ...selectedSection, in: true });
        }, transitionDelay);
    };

    const resetSelection = () => {
        if (selectedSection.current.id === "compare") TelemetryService.sendCompareData();
        window.history.pushState({}, document.title, "/");
        setSelectedSection({ ...selectedSection, in: false });
        setSelected(null);
        setQueriedResult(null)
    };

    const onTransitionExited = (setCurrent = false) => {
        setSelectedSection({
            ...selectedSection,
            current: setCurrent ? selected : null
        });
    };

    const getInnerSlideData = type => {
        const reducer = (prev, curr) => ({ ...prev.innerSlides, ...curr.innerSlides });
        const innerSlides = slide.content.sections.reduce(reducer);
        return Object.values(innerSlides).filter(slide => slide.type === type)[0];
    };

    const handleResultQuery = useCallback(() => {
        const result = queryParams.get(queryParamsId.result);

        if (result) {
            if (!selectedSection.current) setSelectedSection({ current: slide.content.sections[0], in: true });
            setQueriedResult(result);
        }
    }, [queryParams, selectedSection, slide.content.sections]);

    const handleSectionQuery = useCallback(() => {
        if (!queryParams.has(queryParamsId.section) || selectedSection.current) return;

        switch (queryParams.get(queryParamsId.section)) {
            case "help-me-choose":
                HelpMeChooseService.initialize();
                return setSelectedSection({ current: slide.content.sections[0], in: true });
            case "compare":
                setSelectedSection({ current: slide.content.sections[1], in: true });
                setQueriedDevices({
                    left: queryParams.get(queryParamsId.devices.left),
                    right: queryParams.get(queryParamsId.devices.right)
                });
                return;
            default:
                return;
        }
    }, [queryParams, selectedSection, slide.content.sections]);

    useEffect(() => {
        handleResultQuery();
        handleSectionQuery();
    }, [handleResultQuery, handleSectionQuery]);

    useEffect(() => {
        setCanSwipe(selectedSection.current ? false : true);
        document.documentElement.classList = selectedSection.current ? "" : "disable-default-touch";
    }, [selectedSection, setCanSwipe]);

    return (
        <div className={styles.selectionSlide}>
            {!selectedSection.current && (
                <CSSTransition
                    in={!selectedSection.in}
                    appear
                    timeout={transitionDelay / 2}
                    onExited={() => onTransitionExited(true)}
                >
                    {state => (
                        <div className={`${styles.animationWrapper} ${styles[state]}`}>
                            <Heading
                                title={slide.content.title}
                                subtitle={slide.content.subtitle}
                            />

                            <OptionsWrapper>
                                {slide.content.sections.map((section, i) => (
                                    <OptionCard
                                        key={`${section.id}-option`}
                                        selected={selected === section}
                                        option={section}
                                        handleOnClick={() => setSection(section)}
                                    />
                                ))}
                            </OptionsWrapper>
                        </div>
                    )}
                </CSSTransition>
            )}

            {selectedSection.current && (
                <CSSTransition
                    in={selectedSection.in}
                    timeout={transitionDelay / 2}
                    onExited={onTransitionExited}
                >
                    {state => (
                        <div className={`${styles.animationWrapper} ${styles[state]}`}>
                            {selectedSection.current.id === "help-me-choose" && (
                                <HelpMeChooseSection
                                    loadingSlide={getInnerSlideData("loading")}
                                    resultsSlide={getInnerSlideData("results")}
                                    {...{ queriedResult }}
                                />
                            )}

                            {selectedSection.current.id === "compare" && (
                                <CompareSection
                                    comparisonTable={selectedSection.current.comparisonTable}
                                    {...{ queriedDevices }}
                                />
                            )}

                            {selectedSection.current.id && (
                                <button className={styles.closeBtn} onClick={resetSelection}>
                                    <i className="ms-Icon ms-Icon--ChromeClose" />
                                </button>
                            )}
                        </div>
                    )}
                </CSSTransition>
            )}
        </div>
    );
};

SelectionSlide.propTypes = {
    slide: PropTypes.object.isRequired
};

export default SelectionSlide;
