import React, { useRef, useState, useEffect } from "react";
import PropTypes from "prop-types";
import useEventListener from "../../../../../../../hooks/useEventListener";
import styles from "./OptionsWrapper.module.scss";

const OptionsWrapper = ({ children }) => {
    const wrapper = useRef();
    const isDragging = useRef(false);
    const dragOffset = useRef(0);
    const [hasOverflow, setHasOverflow] = useState(false);
    const [scrollPosition, setScrollPosition] = useState(0);

    const checkOverflow = () => {
        setHasOverflow(wrapper.current.scrollWidth > wrapper.current.clientWidth);
    };

    const handlePointerDown = ({ clientX }) => {
        if (!hasOverflow) return;
        isDragging.current = true;
        dragOffset.current = clientX;
    };

    const handlePointerMove = ({ clientX }) => {
        const swipeThreshold = 50;
        const reachedMinThreshold = Math.abs(clientX - dragOffset.current) > swipeThreshold;
        const cardWidth = wrapper.current.firstChild.clientWidth;

        if (!isDragging.current || !hasOverflow || !reachedMinThreshold) return;

        if (clientX > dragOffset.current) wrapper.current.scrollLeft -= cardWidth;
        else wrapper.current.scrollLeft += cardWidth;

        setScrollbarPosition();
    };

    const handlePointerUp = () => {
        if (!hasOverflow) return;
        isDragging.current = false;
        dragOffset.current = 0;
    };

    const setScrollbarPosition = () => {
        if (!hasOverflow) return;
        const scroll = wrapper.current.scrollLeft;
        const width = wrapper.current.scrollWidth - wrapper.current.clientWidth;
        const position = (scroll / width) * 100;
        setScrollPosition(position);
    };

    useEffect(checkOverflow, []);
    useEventListener("resize", () => {
        checkOverflow();
        setScrollbarPosition();
    });

    return (
        <>
            <div
                ref={wrapper}
                onPointerUp={handlePointerUp}
                onPointerDown={handlePointerDown}
                onPointerMove={handlePointerMove}
                onScroll={setScrollbarPosition}
                className={`
                    ${styles.optionsWrapper}
                    ${hasOverflow ? styles.hasOverflow : ""}
                `}
            >
                {children}
            </div>

            {hasOverflow && (
                <div
                    className={styles.optionsScrollbar}
                    style={{ "--optionsScrollbarPos": `${scrollPosition}%` }}
                />
            )}
        </>
    );
};

OptionsWrapper.propTypes = {
    children: PropTypes.node.isRequired
};

export default OptionsWrapper;
