import { ConfigurationService } from "./ConfigurationService";
import { LegacyConfigurationService } from "./LegacyConfigurationService";
import LoggingService from "./LoggingService";
import TelemetryService from "./TelemetryService";

const flattenArray = array => [].concat(...array);
const sortFn = (x, y) => y.totalWeight - x.totalWeight;

class AppHelpMeChooseService
{
    questions = [];
    devices = [];
    currentQuestion = null;
    currentQuestionIndex = 0;
    preSelectionType = null;
    selections = {};
    initialized = false;

    constructor(configurationService, preSelectionChoiceId)
    {
        this.configurationService = configurationService;
        this.preSelectionChoiceId = preSelectionChoiceId;
    }

    get canGoBack()
    {
        return this.currentQuestionIndex > 0;
    }

    get canGoForward()
    {
        return Object.keys(this.selections[this.currentQuestion.id]).length > 0;
    }

    get isLastQuestion()
    {
        return this.currentQuestionIndex === this.questions.length - 1;
    }

    initialize()
    {
        this.questions = this.configurationService.getSectionQuestions("help-me-choose");
        this.setCurrentQuestion(0);
        this.questions.forEach(question =>
        {
            this.selections[question.id] = {};
        });
        this.initialized = true;
    }

    getQuestions()
    {
        return this.questions;
    }

    getCurrentSelection()
    {
        return Object.keys(this.selections[this.currentQuestion.id]);
    }

    getCurrentQuestion()
    {
        return this.currentQuestion;
    }

    getSelections()
    {
        return this.selections;
    }

    getDevices()
    {
        return this.devices;
    }

    getFinalSelections()
    {
        const selectedOptions = flattenArray(
            Object.values(this.selections).map(selection => Object.values(selection))
        );

        return flattenArray(selectedOptions).filter(option => !option.excluding);
    }

    getCurrentQuestionIndex()
    {
        return this.currentQuestionIndex;
    }

    getPreSelectionType()
    {
        const currentQuestionIsGateway = this.currentQuestion.id === this.preSelectionChoiceId;
        return !currentQuestionIsGateway ? this.preSelectionType : null;
    }

    setQuestions(questions)
    {
        this.questions = questions;
        this.setCurrentQuestion(0);
    }

    setCurrentQuestion(questionIndex)
    {
        this.currentQuestion = this.questions[questionIndex];
        this.currentQuestionIndex = questionIndex;
        return this.getCurrentQuestion();
    }

    calcWeight()
    {
        const selections = this.getFinalSelections();
        const results = this.devices
            .map(device => ({
                ...device,
                totalWeight: device.optionsWeights
                    .filter(optionWeight => selections.find(selection => selection.id === optionWeight.type) !== undefined)
                    .reduce((p, c) => p + c.weight, 0)
            }))
            .sort(sortFn);
            
        TelemetryService.sendHelpMeChooseData({
            selectedValues: [...new Set(selections.map(x => x.id))].join(", "),
            result: results[0].name
        });
        
        return results;
    }

    setSelection(option)
    {
        if (!this.currentQuestion.multipleChoice) this.selections[this.currentQuestion.id] = {};

        const questionSelection = this.selections[this.currentQuestion.id];

        if (questionSelection.hasOwnProperty(option.id)) delete questionSelection[option.id];
        else
        {
            if (option.excluding)
            {
                Object.keys(questionSelection).forEach(key => delete questionSelection[key]);
            }
            else
            {
                Object.keys(questionSelection).forEach(key =>
                {
                    const option = this.currentQuestion.options.find(option => option.id === key);
                    if (option.excluding) delete questionSelection[key];
                });
            }

            questionSelection[option.id] = option;
        }

        return this.getCurrentSelection();
    }

    setPreSelectionType(type = "default")
    {
        this.preSelectionType = type;
    }

    setDevices(devices)
    {
        const filteredDevices = devices.filter(device => !device.legacyConfigTag || LegacyConfigurationService.getBoolean(device.legacyConfigTag) !== false);
        filteredDevices.forEach(device => device.colors = device.colors.filter(color => !color.legacyConfigTag || LegacyConfigurationService.getBoolean(color.legacyConfigTag) !== false));
        this.devices = filteredDevices;
        LoggingService.info(`Devices filtered: [${JSON.stringify(this.devices.map(x => x.name))}]`);
    }

    questionHasSelections()
    {
        return this.getCurrentSelection().length > 1;
    }

    goForward()
    {
        if (this.currentQuestionIndex === this.questions.length - 1) return;

        if (this.currentQuestion.id === this.preSelectionChoiceId)
        {
            const currentSelections = this.getCurrentSelection();

            // we leave setPreSelectionType with no arguments
            // because dynamic background is not needed anymore
            if (currentSelections.length > 1) this.setPreSelectionType();
            else this.setPreSelectionType();
        }

        return this.setCurrentQuestion(this.currentQuestionIndex + 1);
    }

    goBack()
    {
        if (this.currentQuestionIndex === 0) return;

        return this.setCurrentQuestion(this.currentQuestionIndex - 1);
    }

    startOver = () =>
    {
        // clear selections
        for (let key in this.selections)
        {
            this.selections[key] = {};
        }

        this.setCurrentQuestion(0);
    };
}

export default new AppHelpMeChooseService(ConfigurationService, "gateway");
