import React from "react";
import PropTypes from "prop-types";
import SelectionSlide from "../Slides/SelectionSlide";

const SlideSwitch = ({ slide, slideSection }) => {
    const sharedProps = {
        slide,
        slideSection
    };

    switch (slide.config.layout) {
        case "selection-slide":
            return <SelectionSlide {...sharedProps} key={slide.config.id} />;

        default:
            return (
                <div className="centered-message">
                    <h2>We didn't find a proper slide to display the content</h2>
                </div>
            );
    }
};

SlideSwitch.propTypes = {
    slide: PropTypes.object.isRequired,
    slideSection: PropTypes.string
};

export default SlideSwitch;
