import { ScreenResolution } from "../models/ScreenResolution";
import LoggingService from "./LoggingService";

export class CacheService
{
    cacheObject = null;

    maxSize = -1;

    cacheKey;

    /**
     * Preload the slides resources.
     * @param {string} cache the cache key.
     * @param {number} maxSize the file max size in MB.
     */
    constructor(cacheKey = 'cache', maxSize = -1)
    {
        this.cacheKey = cacheKey;
        this.maxSize = (maxSize * 1000000);
    }

    /**
     * Gets the cache object.
     */
    async getCache()
    {
        if (this.cacheObject === null && window.caches)
        {
            this.cacheObject = await window.caches.open(this.cacheKey);
        }
        return this.cacheObject;
    }

    /**
     * Download and add to cache the files in the specified resolution.
     * @param {string} imagesUrls the files urls.
     * @param {ScreenResolution} screenResolution the screen resolution.
     * @param {boolean} saveToBlob save the files to blob.
     * @param {boolean} checkFileExists checks if the file exists, and the resolution.
     */
    async downloadFileList(imagesUrls, screenResolution, saveToBlob, checkFileExists)
    {
        return await Promise.all(imagesUrls.map(x => this.downloadFile(x, screenResolution, saveToBlob, checkFileExists)));
    }

     /**
     * Download and add to cache the file in the specified resolution.
     * @param {string} url the file url.
     * @param {ScreenResolution} screenResolution the screen resolution.
     * @param {boolean} saveToBlob save the file to blob.
     * @param {boolean} checkFileExists checks if the file exists, and the resolution.
     */
    async downloadFile(url, screenResolution, saveToBlob, checkFileExists)
    {
        if (url === '' || typeof URL.createObjectURL !== 'function')
            return url;

        const urlToDownload = await this.getResolutionUrl(url, screenResolution, checkFileExists);

        if (!urlToDownload)
            return url;

        if (!saveToBlob)
            return urlToDownload;

        const blob = await this.cacheUrlBlob(urlToDownload);
        return blob ? URL.createObjectURL(blob) : url;
    }

    /**
     * Gets the url for the provided screen resolution.
     * @param {string} url the url.
     * @param {ScreenResolution} screenResolution the screen resolution.
     * @param {boolean} checkFileExists checks if the file exists, and the resolution.
     */
    async getResolutionUrl(url, screenResolution, checkFileExists)
    {
        let resolutionUrl = null;

        switch (screenResolution)
        {
            case ScreenResolution.UHDV:
                resolutionUrl = await this.buildResolutionUrl(url, ScreenResolution.UHDV, checkFileExists);
                resolutionUrl = resolutionUrl ? resolutionUrl : await this.getResolutionUrl(url, ScreenResolution.FULLHD, checkFileExists);
                break;

            case ScreenResolution.FULLHD:
                resolutionUrl = await this.buildResolutionUrl(url, ScreenResolution.FULLHD, checkFileExists);
                resolutionUrl = resolutionUrl ? resolutionUrl : await this.getResolutionUrl(url, ScreenResolution.HD, checkFileExists);
                break;

            case ScreenResolution.HD:
                resolutionUrl = await this.buildResolutionUrl(url, ScreenResolution.HD, checkFileExists);
                break;

            default:
                break;
        }

        return resolutionUrl ? resolutionUrl : url;
    }

    /**
     * Builds the url for the provided screen resolution.
     * @param {string} url the url.
     * @param {ScreenResolution} screenResolution the screen resolution.
     * @param {boolean} checkFileExists checks if the file exists, and the resolution.
     */
    async buildResolutionUrl(url, screenResolution, checkFileExists)
    {
        const filename = url.replace(/^.*[\\/]/, '').replace(/\.[^/.]+$/, '');
        const resolutionUrl = url.replace(filename, `${filename}-${screenResolution}`);

        if (!checkFileExists)
            return resolutionUrl;

        return  await this.resourceIsValid(resolutionUrl) ? resolutionUrl : null;
    }

    /**
     * Gets if resource exists and is valid.
     * @param {string} url the resource url.
     */
    async resourceIsValid(url)
    {
        const request = new Request(url);
        const response = await fetch(request, {'method': 'HEAD'});

        if (!response.ok)
            return false;

        if (response.headers.has('Content-Type') &&
            !response.headers.get('Content-Type').includes('image'))
            return false;

        if (this.maxSize > 0 &&
            response.headers.has('content-length') &&
            response.headers.get('content-length') > this.maxSize)
        {
            LoggingService.warn(`resource too big ${url} ${response.headers.get('content-length')}`);
            return false;
        }

        return true;
    }

    /**
     * Saves the resource from url in a Blob.
     * @param {string} url the resource url.
     */
    async cacheUrlBlob(url)
    {
        const cache = await this.getCache();
        if (!cache)
            return;

        const request = new Request(url);
        const cachedResponse = await cache.match(request);

        if (!cachedResponse)
        {
            LoggingService.log(`Beginning to fetch url ${url}`);
            const response = await fetch(request);
            const blob = response.clone().blob();
            LoggingService.log(`Beginning to cache url ${url}`);
            await cache.put(request, response);
            LoggingService.log(`Stored on cache ${url}`);
            return blob;
        } else {
            LoggingService.log(`Beginning to read url ${url}...`);
            const blob = await cachedResponse.blob();
            LoggingService.log(`Read url ${url} done of size ${blob.size}...`);
            return blob;
        }
    }
}