import { useEffect } from "react";
import TelemetryService from "../services/TelemetryService";

/**
 * Measures the time spent on any given section
 *
 * @param {string} section - The section name
 */
const useSectionTimeTelemetry = section => {
    useEffect(() => {
        const startTime = new Date();

        return () => {
            const endTime = new Date();
            const timeSpent = endTime.getTime() - startTime.getTime();
            TelemetryService.setSectionSwitch({ section, timeSpent });
        };
    }, [section]);
};

export default useSectionTimeTelemetry;
