import React from "react";
import PropTypes from "prop-types";
import showDetailsLogo from "../../../../../assets/images/show_details.png";
import styles from "./ShowDetailsBtn.module.scss";

const ShowDetailsBtn = ({ handleOnClick, label }) => {
    return (
        <button className={styles.showDetailsBtn} onClick={handleOnClick}>
            <div className={styles.imageContainer}>
                <img src={showDetailsLogo} alt="Show details thumbnail" />
            </div>
            <span>{label}</span>
        </button>
    );
};

ShowDetailsBtn.propTypes = {
    handleOnClick: PropTypes.func.isRequired,
    label: PropTypes.string
};

export default ShowDetailsBtn;
