import React, { useState, useCallback, useEffect } from "react";
import PropTypes from "prop-types";
import useSectionTimeTelemetry from "../../../../hooks/useSectionTimeTelemetry";
import HelpMeChooseService from "../../../../services/HelpMeChooseService";
import TelemetryService from "../../../../services/TelemetryService";
import LaptopShowcase from "./LaptopShowcase";
import ShowDetailsBtn from "./ShowDetailsBtn";
import ComparisonTable from "./ComparisonTable";
import styles from "./CompareSection.module.scss";

const CompareSection = ({ comparisonTable, queriedDevices }) => {
    const devices = HelpMeChooseService.getDevices();
    const [tableVisible, toggleTableVisibility] = useState(false);
    const [showMore, toggleShowMore] = useState(false);
    const [leftDevice, setLeftDevice] = useState(null);
    const [rightDevice, setRightDevice] = useState(null);
    const showDisclaimers = showMore && tableVisible;

    const setIndex = useCallback(
        (comparisonIndex, initialIndex) => {
            if (comparisonIndex === initialIndex) return initialIndex < devices.length - 1 ? ++initialIndex : 0;
            else return initialIndex;
        },
        [devices]
    );

    useEffect(() => {
        if (leftDevice !== null && rightDevice !== null) return;

        const filteredLeft = devices.filter(device => device.id === queriedDevices.left)[0];
        const filteredRight = devices.filter(device => device.id === queriedDevices.right)[0];
        const leftIndex = devices.indexOf(filteredLeft);
        const rightIndex = devices.indexOf(filteredRight);
        const initialIndex = { left: 0, right: 1 };

        if (leftIndex !== -1) setLeftDevice(leftIndex);
        else setLeftDevice(setIndex(rightIndex, initialIndex.left));

        if (rightIndex !== -1 && rightIndex !== leftIndex) setRightDevice(rightIndex);
        else setRightDevice(setIndex(leftIndex, initialIndex.right));
    }, [devices, leftDevice, rightDevice, queriedDevices, setIndex]);

    useEffect(() => {
        if (leftDevice === null && rightDevice === null) return;

        TelemetryService.setCompareData({
            device1: devices[leftDevice],
            device2: devices[rightDevice]
        });
    }, [devices, leftDevice, rightDevice]);

    useSectionTimeTelemetry("Compare");

    return (
        <div className={`${styles.compareSection} ${tableVisible ? styles.tableActive : ""}`}>
            {leftDevice !== null && (
                <LaptopShowcase
                    laptops={devices}
                    current={leftDevice}
                    currentComparison={rightDevice}
                    setCurrentDevice={setLeftDevice}
                    showDisclaimers={showDisclaimers}
                    {...{ tableVisible }}
                />
            )}

            {leftDevice !== null && rightDevice !== null && (
                <>
                    {!tableVisible && (
                        <ShowDetailsBtn
                            label={comparisonTable.showDetailsBtn}
                            handleOnClick={() => toggleTableVisibility(true)}
                        />
                    )}

                    {tableVisible && (
                        <ComparisonTable
                            {...{
                                devices,
                                leftDevice,
                                rightDevice,
                                setLeftDevice,
                                setRightDevice,
                                comparisonTable,
                                showMore,
                                handleClose: () => toggleTableVisibility(false),
                                toggleShowMore: () => toggleShowMore(!showMore)
                            }}
                        />
                    )}
                </>
            )}

            {rightDevice !== null && (
                <LaptopShowcase
                    laptops={devices}
                    current={rightDevice}
                    currentComparison={leftDevice}
                    setCurrentDevice={setRightDevice}
                    showDisclaimers={showDisclaimers}
                    disclaimersToRight={true}
                    {...{ tableVisible }}
                />
            )}
        </div>
    );
};

CompareSection.propTypes = {
    comparisonTable: PropTypes.object.isRequired,
    queriedDevices: PropTypes.object
};

export default CompareSection;
