import React, { Component } from "react";
import PropTypes from "prop-types";
import styles from "../Navbar.module.scss";

class NavSection extends Component {
    navIds = [];
    sectionIsActive;

    componentDidMount() {
        if (this.props.navigation) {
            this.navIds = this.props.navigation.slides.map(x => x.id);
        }
    }

    componentDidUpdate(prevProps) {
        const { navigation, currentSlide, setActiveSlide } = this.props;

        if (prevProps.currentSlide !== currentSlide) {
            const sectionIsActive = this.navIds.includes(currentSlide);
            if (sectionIsActive) setActiveSlide(navigation.title);
        }
    }

    render() {
        const { currentSlide, navigation, onSectionClick } = this.props;
        const sectionIsActive = this.navIds.includes(currentSlide);

        return (
            <li className={sectionIsActive ? styles.active : ""}>
                <button className={styles.navButton} onClick={() => onSectionClick(navigation)}>
                    {navigation.title}
                </button>
            </li>
        );
    }
}

NavSection.propTypes = {
    navigation: PropTypes.object.isRequired,
    currentSlide: PropTypes.string,
    onSectionClick: PropTypes.func.isRequired,
    setActiveSlide: PropTypes.func.isRequired
};

export default NavSection;
