import { PlatformDetectionService } from "./PlatformDetectionService";
import LoggingService, { LogType } from "./LoggingService";
import ResourcePreloadService from "./ResourcePreloadService";
import TelemetryService from "./TelemetryService";
import { LogProvider } from "./providers/LogProvider";
import { InMemoryLogProvider } from "./providers/InMemoryLogProvider";
import { PreloadProvider } from "./providers/PreloadProvider";
import { RdaPreloadProvider } from "./providers/RdaPreloadProvider";
import { WebTelemetryProvider, SdxTelemetryProvider, RdxTelemetryProvider } from "./providers/TelemetryProvider";

export class InitializationService
{
    static initialize()
    {
        if (PlatformDetectionService.isUwp())
        {
            LoggingService.setLogProvider(new InMemoryLogProvider());
            ResourcePreloadService.setPreloadProvider(new RdaPreloadProvider());

            if (window.SDX)
                TelemetryService.setTelemetryProvider(new SdxTelemetryProvider())
            else if (window.RDX || window.parent?.RDX)
                TelemetryService.setTelemetryProvider(new RdxTelemetryProvider())
        }
        else
        {
            LoggingService.setLogProvider(new LogProvider());
            ResourcePreloadService.setPreloadProvider(new PreloadProvider());
            TelemetryService.setTelemetryProvider(new WebTelemetryProvider())
        }

        LoggingService.setMinimumLevel(LogType.Debug);
        LoggingService.info(`Initialized app in ${PlatformDetectionService.isUwp() ? 'uwp' : 'web'} mode`);
        TelemetryService.initTelemetryProvider();
    }
}