import React from "react";
import HelpMeChooseService from "../../../../../../services/HelpMeChooseService";
import styles from "./Progress.module.scss";

const Progress = ({ currentQuestion }) => {
    const questions = HelpMeChooseService.getQuestions();
    const currentIndex = HelpMeChooseService.getCurrentQuestionIndex();
    const progressClassName = `progress_${currentQuestion.id}`;

    return (
        <div
            className={`
                ${styles.progress}
                ${styles[progressClassName]}
            `}
        >
            {questions.map((_, i) => (
                <div
                    key={`question-${i}`}
                    style={{ animationDelay: `${i * 50}ms` }}
                    className={`${styles.progressDot} ${i <= currentIndex ? styles.isActive : ""}`}
                />
            ))}
        </div>
    );
};

export default Progress;
