import React, { useState } from "react";
import PropTypes from "prop-types";
import { CarouselContextProvider } from "../../contexts/CarouselContext";
import Carousel from "../Carousel";
import useWindowWidth from "../../hooks/useWindowWidth";

const CarouselSettings = ({ slides, initialSlide, navigations, navDisabled }) => {
    const isDesktop = useWindowWidth(900);
    const [canSwipe, setCanSwipe] = useState(true);

    return (
        <CarouselContextProvider value={{ isDesktop, setCanSwipe }}>
            <Carousel
                {...{
                    slides,
                    initialSlide,
                    navigations,
                    navDisabled,
                    theme: "light",
                    swipeThreshold: 75,
                    canSwipe
                }}
            />
        </CarouselContextProvider>
    );
};

Carousel.propTypes = {
    slides: PropTypes.object.isRequired,
    initialSlide: PropTypes.object.isRequired,
    navigations: PropTypes.array.isRequired,
    navDisabled: PropTypes.bool.isRequired
};

export default CarouselSettings;
