import { TelemetryEvent } from "../models/TelemetryEvent";

export class ApplicationTelemetryService
{
    /**
     * @typedef {{ section: string, timeSpent: number }} Section
     * @typedef {{ selectedValues: string, result: string }} HelpMeChooseData
     * @typedef {{ device1: Object, device2: Object }} Comparison
     */

    previousCompare = null;
    compareData = [];

    /**
     * Set the telemetry provider.
     * @param {Object} telemetryProvider the telemetry provider.
     */
    setTelemetryProvider(telemetryProvider)
    {
        this.telemetryProvider = telemetryProvider;
    }

    /**
     * Initializes de telemetry provider
     */
    initTelemetryProvider()
    {
        try
        {
            this.telemetryProvider.init();
        } catch (e)
        {
            console.error(e);
        }
    }

    /**
     * The app's activity in milliseconds
     *
     * @param {number} time The carousel's activity time
     */
    timeCarousel(time)
    {
        try
        {
            this.sendTelemetry(TelemetryEvent.TimedCarousel, { time });
        } catch (e)
        {
            console.error(e);
        }
    }

    /**
     * Measures the time spend on each section
     *
     * @param {Section} section - The section data
     */
    setSectionSwitch(section)
    {
        try
        {
            this.sendTelemetry(TelemetryEvent.SwitchedSection, section);
        } catch (e)
        {
            console.error(e);
        }
    }

    /**
     * Sets the data from the Compare section
     *
     * @param {Comparison} comparison - The comparison and its duration
     */
    setCompareData(comparison)
    {
        try
        {
            if (
                this.previousCompare &&
                (this.previousCompare.device1.id === comparison.device1.id || this.previousCompare.device2.id === comparison.device2.id)
            )
            {
                const { device1, device2 } = this.previousCompare;
                const timeSpent = new Date().getTime() - this.previousCompare.compareTimeStart;
                const string = `${device1.name} vs ${device2.name} - Comparison time: ${timeSpent}ms`;
                this.compareData.push(string);
            }

            this.previousCompare = {
                device1: comparison.device1,
                device2: comparison.device2,
                compareTimeStart: new Date().getTime()
            };
        } catch (e)
        {
            console.error(e);
        }
    }

    /**
     * Dispatchs the event for Help Me Choose Section
     *
     * @param {HelpMeChooseData} data - The data to send
     */
    sendHelpMeChooseData(data)
    {
        try
        {
            this.sendTelemetry(TelemetryEvent.HelpMeChooseResult, data);
        } catch (e)
        {
            console.error(e);
        }
    }

    /**
     * Dispatchs the event for Compare Section
     */
    sendCompareData()
    {
        if (!this.compareData.length) return;
        try
        {
            const data = { devices: this.compareData.join(", ") };
            this.sendTelemetry(TelemetryEvent.SideBySideComparissons, data);
            this.compareData = [];
        } catch (e)
        {
            console.error(e);
        }
    }

    /**
     * Dispatchs the event for the correspondant platform
     *
     * @param {string} eventName - The event identifier
     * @param {Object} properties - The event properties
     */
    sendTelemetry(eventName, properties)
    {
        this.telemetryProvider.send(eventName, properties);
    }
}

export default new ApplicationTelemetryService();
