import React from "react";
import PropTypes from "prop-types";
import styles from "./Disclaimer.module.scss";
import ScrollBox from "../ScrollBox";

const Disclaimer = ({ disclaimers, customStyles, isScrollable }) => {
    const setDisclaimers = () => {
        return disclaimers.map((disclaimer, i) => <small key={i}>{disclaimer}</small>);
    };

    return (
        <div className={`${styles.disclaimer} ${customStyles ? customStyles : ""}`}>
            {isScrollable && <ScrollBox threshold={80}>{setDisclaimers()}</ScrollBox>}
            {!isScrollable && setDisclaimers()}
        </div>
    );
};

Disclaimer.propTypes = {
    disclaimers: PropTypes.array.isRequired,
    customStyles: PropTypes.string,
    isScrollable: PropTypes.bool
};

export default Disclaimer;
