import React, { useState } from "react";
import PropTypes from "prop-types";
import useWindowWidth from "../../../hooks/useWindowWidth";
import styles from "./SlideBackground.module.scss";

const SlideBackground = ({ background }) => {
    const [imgLoaded, setImgLoaded] = useState(false);
    const [placeholderVisible, setPlaceholderVisibility] = useState(true);
    const isDesktop = useWindowWidth(800);

    return (
        <>
            {placeholderVisible && (
                <img
                    alt="Slide background placeholder"
                    src={isDesktop ? background.placeholder : background.mobilePlaceholder}
                    className={`${styles.questionBackground} ${styles.backgroundPlaceholder}`}
                />
            )}

            <img
                alt="Slide background"
                src={isDesktop ? background.desktop : background.mobile}
                className={`${styles.questionBackground} ${imgLoaded ? styles.backgroundActive : ""}`}
                onLoad={() => setImgLoaded(true)}
                onAnimationEnd={() => setPlaceholderVisibility(false)}
            />
        </>
    );
};

SlideBackground.propTypes = {
    background: PropTypes.object.isRequired
};

export default SlideBackground;
