class CSSService {
    /**
     * Asigns a custom property to any given element (Root by default)
     *
     * @param {string} name - The name of the custom property
     * @param {string | number} value - The value of the custom property
     * @param {HTMLElement} target - The target element
     */
    static setCustomProperty(name, value, target = document.documentElement) {
        target.style.setProperty(`--${name}`, value);
    }
}

export default CSSService;
