import { ApplicationInsights } from "@microsoft/applicationinsights-web";
import { DeviceInformation, RdxDeviceInformation, webDeviceInformation } from "../../models/DeviceInformation";

export class TelemetryProvider {
    init() { }

    send(eventName, properties) { }

    addDefaultProperties(properties) {
        return {
            ...properties,
            appCodeName: DeviceInformation.appCodeName,
            appName: DeviceInformation.appName,
            appVersion: DeviceInformation.appVersion,
            cookieEnabled: DeviceInformation.cookieEnabled,
            language: DeviceInformation.language,
            onLine: DeviceInformation.onLine,
            platform: DeviceInformation.platform,
            userAgent: DeviceInformation.userAgent,
            origin: DeviceInformation.origin
        };
    }
}

export class RdxTelemetryProvider extends TelemetryProvider {
    send(eventName, properties) {
        const telemetryClient = window.parent && window.parent.TelemetryClient ? window.parent.TelemetryClient : window.TelemetryClient;

        if (!process.env.REACT_APP_RDA_INSTRUMENTATION_KEY || !telemetryClient)
            return;

        telemetryClient.trackEventProperties(process.env.REACT_APP_RDA_INSTRUMENTATION_KEY, eventName, JSON.stringify(this.addDefaultProperties(properties)));
    }

    addDefaultProperties(properties) {
        return {
            ...super.addDefaultProperties(properties),
            RetailAccessCode: RdxDeviceInformation.retailAccessCode,
            Region: RdxDeviceInformation.Region,
            StoreID: RdxDeviceInformation.StoreID,
            SystemSKU: RdxDeviceInformation.SystemSKU,
            SKU: RdxDeviceInformation.SKU,
            MachineId: RdxDeviceInformation.MachineId
        };
    }
}

export class SdxTelemetryProvider extends TelemetryProvider {
    send(eventName, properties) {
        window.SDX.trackEvent(eventName, JSON.stringify(this.addDefaultProperties(properties)));
    }
}

export class WebTelemetryProvider extends TelemetryProvider {
    constructor() {
        super();

        if (!process.env.REACT_APP_WEB_INSTRUMENTATION_KEY) return;
        this.appInsights = new ApplicationInsights({ config: { instrumentationKey: process.env.REACT_APP_WEB_INSTRUMENTATION_KEY } });
        this.appInsights.loadAppInsights();
    }

    init() {
        webDeviceInformation.setDefaultProperties();
    }

    addDefaultProperties(properties) {
        return {
            ...super.addDefaultProperties(properties),
            sessionId: webDeviceInformation.sessionId,
            RAC: webDeviceInformation.retailAccessCode,
            StoreID: webDeviceInformation.storeID,
            SKU: webDeviceInformation.sku,
            Source: webDeviceInformation.sourceId,
        };
    }

    send(eventName, properties) {
        if (!this.appInsights) return;
        this.appInsights.trackEvent(eventName, this.addDefaultProperties(properties));
    }
}
