import { Translator } from "./Translator";

export class Exporter
{
    strings = "";
    translator = new Translator();

    async exportLanguage(application, language)
    {
        this.strings = "key,value\n";

        for (var model of application.models)
        {
            // 1. loads navigation & model.
            await this.loadFile(`/configuration/navigations/${model.value}.js`).catch(e => console.error(e));
            await this.loadFile(`/configuration/models/${model.value}.js`).catch(e => console.error(e));
            await this.loadFile(`/configuration/devices/devices.js`).catch(e => console.error(e));

            // 2. if language was provided, try to update the model.
            if (language)
            {
                await this.loadFile(`/configuration/languages/${language}.js`).catch(e => console.error(e));
                this.translator.applyLanguage(window.carouselAppNavigations, window.carouselAppLanguage, `${model.value}.navigation`);
                this.translator.applyLanguage(window.carouselAppModel, window.carouselAppLanguage, model.value);
                this.translator.applyLanguage(window.carouselAppDevices, window.carouselAppLanguage, "devices");
            }

            // 3. prints the string.
            this.printStrings(window.carouselAppNavigations, `${model.value}.navigation`);
            this.printStrings(window.carouselAppModel, model.value);
            this.printStrings(window.carouselAppDevices, `devices`);
        }

        this.downloadScript();
    }

    printStrings(model, path)
    {
        if (path.indexOf('config.type') > 0 ||
            path.indexOf('config.layout') > 0 ||
            path.endsWith('.id') ||
            path.endsWith('.type') ||
            path.endsWith('.color') ||
            path.endsWith('.legacyConfigTag') ||
            path.endsWith('.video') ||
            path.endsWith('.keyword') ||
            path.endsWith('.theme') ||
            path.endsWith('.image') ||
            path.endsWith('.icon'))
            return;


        if (typeof model === "string" && (
            model.endsWith(".png") ||
            model.endsWith(".jpg") ||
            model.endsWith(".gif") ||
            model.endsWith(".mp4")))
        {
            return;
        }
        if (typeof model === "string")
        {
            this.strings += `${path},"${model.replace(/"/g, "\\\"")}"\n`;
        }
        else
        {
            for (var key in model)
            {
                if (model.hasOwnProperty(key))
                {
                    this.printStrings(model[key], `${path}.${key}`);
                }
            }
        }
    }

    downloadScript()
    {
        var blob = new Blob([this.strings], { type: 'text/csv' });
        var fileName = "en-us.csv"
        var a = window.document.createElement("a");

        a.href = window.URL.createObjectURL(blob);
        a.download = fileName;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
    }

    loadFile = (path) =>
    {
        return new Promise((resolve, reject) =>
        {
            var script = document.createElement('script');
            script.onload = () => resolve();
            script.onerror = e => reject(e);
            script.src = process.env.PUBLIC_URL + path;
            document.body.appendChild(script);
        });
    };
}