export default class AnimationService {
    static animate(callback, duration) {
        const started = new Date();
        return new Promise((resolve) => {
            function internalAnimate() {
                const elapsed = new Date().getTime() - started.getTime();
                if (duration > 0 && elapsed <= duration) {
                    callback(elapsed / duration);
                    requestAnimationFrame(() => internalAnimate());
                    return;
                }
                callback(1);
                resolve();
            }
            internalAnimate();
        });
    }

    static lerp(from, to, percentage) {
        return (1 - percentage) * from + percentage * to;
    }
}
