import React, { useState } from "react";
import PropTypes from "prop-types";
import HelpMeChooseService from "../../../../../services/HelpMeChooseService";
import useWindowWidth from "../../../../../hooks/useWindowWidth";
import Laptop from "../../../../SlideBlocks/Laptop";
import Disclaimer from "../../../../SlideBlocks/Disclaimer";
import styles from "./Results.module.scss";

const Results = ({ data, startOver, device }) => {
    const result = device || HelpMeChooseService.calcWeight()[0];
    const [disclaimers] = useState(setDisclaimers(result));
    const isDesktop = useWindowWidth(800);

    function setDisclaimers(device) {
        let disclaimers = [];

        device.textContents.forEach(({ disclaimer }) => {
            const isArray = typeof disclaimer === "object";
            if (disclaimer) disclaimers.push(isArray ? disclaimer.join("\n") : disclaimer);
        });

        return disclaimers;
    }

    return (
        <div className={styles.results}>
            {!isDesktop && (
                <button className={styles.startOverBtn} onClick={startOver}>
                    <i className="ms-Icon ms-Icon--Refresh" />
                    <span>{data.restartBtnText}</span>
                </button>
            )}

            <h1>{data.title}</h1>

            <div className={styles.contentWrapper}>
                <div className={styles.resultName}>
                    <h2>{result.name}</h2>
                    <p>{result.text}</p>
                </div>

                <div className={styles.resultSpecs}>
                    <ul className={styles.resultDetails}>
                        {result.textContents.map((content, i) => (
                            <li key={i}>
                                <h3>{content.heading}</h3>
                                <p>{content.paragraph}</p>
                            </li>
                        ))}
                    </ul>

                    {isDesktop && (
                        <button className={styles.startOverBtn} onClick={startOver}>
                            <i className="ms-Icon ms-Icon--Refresh" />
                            <span>{data.restartBtnText}</span>
                        </button>
                    )}

                    <Disclaimer disclaimers={disclaimers} />
                </div>

                <Laptop key={result.id} laptop={result} />
            </div>
        </div>
    );
};

Results.propTypes = {
    data: PropTypes.object.isRequired
};

export default Results;
