class ApplicationLocalizationService {
    /**
     * Gets an specific string from the language file.
     * @param {string} key The object key.
     * @param {Array} [variables = null] The set of variables to replace in the string (optional).
     */
    getAccessibility(key, variables = null) {
        if (!window.carouselAppLanguage) return;

        let string = window.carouselAppLanguage[`accessibility.${key}`];

        if (variables) {
            for (let variable in variables) {
                if (variables.hasOwnProperty(variable)) {
                    const regex = new RegExp(`\\#\\{${variable}\\}`, "g");
                    const replaceText = variables[variable];
                    string = string.replace(regex, replaceText);
                }
            }
        }

        return string;
    }
}

const LocalizationService = new ApplicationLocalizationService();
export default LocalizationService;
