import React from "react";
import PropTypes from "prop-types";
import styles from "./Desktop.module.scss";

const Desktop = ({ option, selected, handleOnClick }) => (
    <button
        className={`${styles.option} ${selected ? styles.selected : ""} ${option.image ? styles.withBackground : styles.withoutBackground}`}
        onClick={handleOnClick}
    >
        {option.image && (
            <>
                <div className={styles.imageContainer}>
                    <img src={option.image} alt={option.text} />
                </div>

                <div className={styles.textAndIconContainer}>
                    <img src={option.icon} alt={`${option.text} icon`} />
                    <h3>{option.text}</h3>
                    {option.description && <p>{option.description}</p>}
                </div>
            </>
        )}

        {!option.image && (
            <>
                <div className={styles.imageContainer}>
                    <img src={option.icon} alt={`${option.text} icon`} />
                </div>

                <h3>{option.text}</h3>
                {option.detail && <small>{option.detail}</small>}
            </>
        )}
    </button>
);

Desktop.propTypes = {
    option: PropTypes.object.isRequired,
    selected: PropTypes.bool,
    handleOnClick: PropTypes.func.isRequired
};

export default Desktop;
