import React, { Component } from "react";
import PropTypes from "prop-types";
import { PlatformDetectionService } from "../../services/PlatformDetectionService";
import styles from "./AdminPanel.module.scss";

class AdminPanel extends Component {
    state = {
        isVisible: false
    };

    componentDidMount() {
        window.addEventListener("keydown", this.handleKeyDown);
    }

    componentWillUnmount() {
        window.removeEventListener("keydown", this.handleKeyDown);
    }

    handleKeyDown = e => {
        if (e.ctrlKey && e.keyCode === 68) {
            if (!PlatformDetectionService.isUwp())
            {
                e.preventDefault();
                if (!this.state.isVisible) this.setState({ isVisible: true });
            }
            else if (window.SDX.toggleAdminPanel)
                window.SDX.toggleAdminPanel();
        }
    };

    onModelChanged = ({ target }) => {
        this.props.onChange(target.value, this.props.language, this.props.slide);
        this.hidePanel();
    };

    onLanguageChanged = ({ target }) => {
        this.props.onChange(this.props.model, target.value, this.props.slide);
        this.hidePanel();
    };

    onSlideChanged = ({ target }) => {
        this.props.onChange(this.props.model, this.props.language, { id: target.value });
        this.hidePanel();
    };

    hidePanel = () => {
        this.setState({ isVisible: false });
    };

    copyUrl = () => {
        const element = document.createElement("textarea");
        const url = window.location.href.split("?");
        element.value = `${url[0]}?app=${this.props.model}&language=${this.props.language}`;

        if (this.props.slide.id) element.value += `&slide=${this.props.slide.id}`;

        document.body.appendChild(element);
        element.select();
        document.execCommand("copy");
        document.body.removeChild(element);
    };

    render() {
        return (
            <>
                {this.state.isVisible && (
                    <div className={styles.wrapper}>
                        <span className={styles.wrapperTitle}>Admin Panel</span>

                        <div className={styles.wrapperControlGroup}>
                            <label>Application</label>
                            <select onChange={this.onModelChanged} value={this.props.model}>
                                {this.props.config.models.map((app, i) => (
                                    <option key={i} value={app.value}>
                                        {app.name}
                                    </option>
                                ))}
                            </select>
                        </div>

                        <div className={styles.wrapperControlGroup}>
                            <label>Language</label>
                            <select onChange={this.onLanguageChanged} value={this.props.language}>
                                {this.props.config.languages.map((language, i) => (
                                    <option key={i} value={language.value}>
                                        {language.name}
                                    </option>
                                ))}
                            </select>
                        </div>

                        <div className={styles.wrapperControlGroup}>
                            <label>Slide</label>
                            <select
                                onChange={this.onSlideChanged}
                                value={this.props.slide.id || ""}
                            >
                                {this.props.slides &&
                                    Object.keys(this.props.slides).map(slide => (
                                        <option key={slide} value={slide}>
                                            {this.props.slides[slide].config.keyword}
                                        </option>
                                    ))}
                            </select>
                        </div>

                        <button className={styles.copyButton} onClick={this.copyUrl}>
                            <i className="ms-Icon ms-Icon--Link" aria-hidden="true" />
                            <span>Copy URL</span>
                        </button>

                        <button onClick={this.props.toggleNavigation}>
                            <i className="ms-Icon ms-Icon--Hide" aria-hidden="true" />
                            <span>{this.props.navDisabled ? "Enable" : "Disable"} navigation</span>
                        </button>

                        <button className={styles.closeButton} onClick={this.hidePanel}>
                            <i className="ms-Icon ms-Icon--ChromeClose" aria-hidden="true" />
                            <span>Close</span>
                        </button>
                    </div>
                )}
            </>
        );
    }
}

AdminPanel.propTypes = {
    config: PropTypes.object.isRequired,
    slides: PropTypes.object,
    model: PropTypes.string.isRequired,
    language: PropTypes.string.isRequired,
    slide: PropTypes.object,
    onChange: PropTypes.func,
    toggleNavigation: PropTypes.func.isRequired,
    navDisabled: PropTypes.bool.isRequired
};

export default AdminPanel;
