import React, { Component } from "react";
import PropTypes from "prop-types";
import CarouselSettings from "../CarouselSettings";
import AdminPanel from "../AdminPanel";
import { Translator } from "../../translations/Translator";
import { Exporter } from "../../translations/Exporter";
import { ConfigurationService } from "../../services/ConfigurationService";
import ResourcePreloadService from "../../services/ResourcePreloadService";
import HelpMeChooseService from "../../services/HelpMeChooseService";
import LoggingService from "../../services/LoggingService";
import LogTerminal from "../LogTerminal";

class ModelConfig extends Component
{
    state = {
        slides: null,
        navigations: null,
        currentModel: "",
        currentLanguage: "",
        config: null,
        initialSlide: null,
        navDisabled: true
    };

    queryParams = new URLSearchParams(this.props.location.search);
    translator = new Translator();
    strings = "";
    exportMode = false;

    async componentDidMount()
    {
        const application = await ConfigurationService.loadApplication();

        this.setState({ config: application });

        if (!application) throw new Error("Couldn't load the configuration file.");

        if (this.queryParams.get("export-language"))
        {
            this.exportMode = true;
            await new Exporter().exportLanguage(window.carouselApp, this.queryParams.get("language"));
            this.setState({
                slides: null,
                currentModel: null,
                currentLanguage: null,
            });
            return;
        }

        const localStorageModel = localStorage.getItem("storedModel");
        const storedModel = application.models.some(({ value }) => value === localStorageModel) ? localStorageModel : application.models[0].value;
        const queriedModel = application.models.find(x => x.value === this.queryParams.get("app"));
        const queriedLanguage = application.languages.find(x => x.value === this.queryParams.get("language"));
        const navigationLanguage = this.getNavigationLanguage(application.languages);
        const defaultLanguage = application.languages.find(language => language.default);
        const newSlide = this.queryParams.get("slide");
        const previousModel = this.queryParams.get("origin");

        await this.setModel(
            queriedModel?.value || storedModel || application.models[0].value,
            queriedLanguage?.value || navigationLanguage?.value || defaultLanguage?.value || application.languages[0].value,
            { id: newSlide, origin: previousModel }
        );
    }

    setModel = async (model, language, slide) =>
    {
        LoggingService.info(`Loading model ${model} in ${language} at ${slide.id}`);

        const models = await ConfigurationService.loadModels(model, language);
        const newSlide = this.getInitialSlide(slide);
        const slides = this.getSlidesFromNavigation();

        document.body.dataset.demo = model;
        await ResourcePreloadService.preloadSlidesResources(slides, newSlide.id, () => this.forceUpdate());
        HelpMeChooseService.setDevices(models.devices);
        
        this.setState(
            {
                slides,
                navigations: models.navigation.content,
                currentModel: model,
                currentLanguage: language,
                initialSlide: newSlide
            },
            () =>
            {
                localStorage.setItem("previousModel", localStorage.getItem("storedModel"));
                localStorage.setItem("storedModel", model);
            }
        );
    };

    getNavigationLanguage = appLanguages => {
        // Get language from "navigator.languages" --> https://mzl.la/3doJm8s
        let locales = navigator.languages;
        if (!locales) {
            // Get language from "navigator.language" --> https://mzl.la/3dqvri9
            locales = [navigator.language ? navigator.language : navigator.userLanguage];
        }

        // Copy array so we can edit it freely
        locales = locales.slice();

        // expand locale preferences array so it includes generic languages if it doesn't currently
        // eg: ["fr-MC", "fr-BE", "en-AU", "fr-LU", "en-GB", "de-DE", "fr"] becomes:
        //     ["fr-MC", "fr-BE", "en-AU", "fr-LU", "en-GB", "en", "de-DE", "de", "fr"]
        let seen = {}
        let i = locales.length;

        while (i--) {
            if (locales[i].indexOf('-') === -1) {
                seen[locales[i]] = true;
            } else {
                let curLang = locales[i].split('-')[0];

                if (!seen[curLang]) {
                    locales.splice(i + 1, 0, curLang);
                    seen[curLang] = true;
                }
            }
        }

        //Loop sorted languages
        for (let locale of locales) {
            let foundFullLocale = appLanguages.find(key => key.value === locale.toLowerCase());
            let foundDerivedLocale = appLanguages.find(key => key.value.indexOf(locale.toLowerCase()) !== -1);
            let language = locale.indexOf("-") !== -1 ? foundFullLocale : foundDerivedLocale;
            if (language) return language;
        }

        // if none is found, return undefined
        return;
    };

    getSlidesFromNavigation = () =>
    {
        const navigations = window.carouselAppNavigations.content;
        const model = window.carouselAppModel;
        const slides = {};

        for (const navigation of navigations)
        {
            for (const slideId of navigation.slides)
            {
                const slide = model[slideId.id];

                if (slide && !slide.config.hidden) slides[slideId.id] = slide;
            }
        }

        return slides;
    };

    getInitialSlide = slide =>
    {
        if (!slide.id)
        {
            const firstNavigation = window.carouselAppNavigations.content[0];
            slide = firstNavigation.defaultSlide
                ? { id: firstNavigation.defaultSlide, ...slide }
                : { id: firstNavigation.slides[0], ...slide };
        }

        return slide;
    };

    adminChanged = (model, language, slide) =>
    {
        this.setState({ slides: null }, () => this.setModel(model, language, slide));
    };

    loadFile = path =>
    {
        return new Promise((resolve, reject) =>
        {
            var script = document.createElement("script");
            script.onload = () => resolve();
            script.onerror = e => reject(e);
            script.src = process.env.PUBLIC_URL + path;
            document.body.appendChild(script);
        });
    };

    toggleNavigation = () =>
    {
        this.setState(
            prevState => ({ navDisabled: !prevState.navDisabled }),
            () => localStorage.setItem("navDisabled", this.state.navDisabled)
        );
    };

    render()
    {
        return this.exportMode === false ? (
            <>
                {this.state.config && (
                    <AdminPanel
                        config={this.state.config}
                        slides={this.state.slides}
                        model={this.state.currentModel}
                        language={this.state.currentLanguage}
                        slide={this.state.initialSlide}
                        navDisabled={this.state.navDisabled}
                        toggleNavigation={this.toggleNavigation}
                        onChange={this.adminChanged}
                    />
                )}

                {this.state.slides && (
                    <CarouselSettings
                        slides={this.state.slides}
                        navigations={this.state.navigations}
                        initialSlide={this.state.initialSlide}
                        navDisabled={this.state.navDisabled}
                    />
                )}

                {!this.state.slides && (
                    <main className="centered-message">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100">
                            <rect fill="#F26B31" y="0" width="48" height="48" />
                            <rect fill="#90C052" x="52" y="0" width="48" height="48" />
                            <rect fill="#FABF1B" x="52" y="52" width="48" height="48" />
                            <rect fill="#3EACDD" y="52" width="48" height="48" />
                        </svg>
                    </main>
                )}

                <LogTerminal />
            </>
        ) : (
                <>
                    <h1>Export Mode</h1>
                    <p>
                        A javascript file should be downloaded. Please take this file, rename it to the
                        proper culture (i.e. en-us, es-ar, fr-fr, etc) and move the file to
                        ./public/configuration/languages.
                </p>
                </>
            );
    }
}

ModelConfig.propTypes = {
    history: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    match: PropTypes.object.isRequired,
    staticContext: PropTypes.object
};

export default ModelConfig;
