class RdxApplicationDatasourceService
{
    /**
     * A datasource object coming from RDX.
     * @type Datasource
     */
    _datasource = {};

    /**
     * Gets the datasource content.
     */
    get datasource() { return this._datasource; }

    /**
     * Initializes the datasource service.
     */
    initialize()
    {
        if (window.carouselAppDatasource)
        {
            this._datasource = window.carouselAppDatasource;
        }
        else if (window.parent && window.parent.datasource)
        {
            // we get the value from the parent window if the app
            // is running in a secondary window opened from a tile link
            // in the main hub content.
            this._datasource = window.parent.datasource;
        }
        else if (window.RDX && window.RDX.datasource)
        {
            // we get the value from the main window if the app
            // is running in the main hub content index.html.
            this._datasource = JSON.parse(window.RDX.datasource);
        }
        else
        {
            this._datasource = {};
        }
    }

    /**
     * Indicates if a given key is present in the registry object.
     * @param {string} key The key to check.
     * @returns {boolean} True if the key exists, false otherwise.
     */
    hasRegistryKey(key)
    {
        return this._datasource.deviceInformation !== undefined &&
            this._datasource.deviceInformation !== null &&
            this._datasource.deviceInformation.RetailInfo !== undefined &&
            this._datasource.deviceInformation.RetailInfo !== null &&
            this._datasource.deviceInformation.RetailInfo[key] !== undefined &&
            this._datasource.deviceInformation.RetailInfo[key] !== null;
    }

    /**
     * Indicates if a given key is present in the sku information object.
     * @param {string} key The key to check.
     * @returns {boolean} True if the key exists, false otherwise.
     */
    hasSkuKey(key)
    {
        return this._datasource.deviceInformation !== undefined &&
            this._datasource.deviceInformation !== null &&
            this._datasource.deviceInformation.skuInformation !== undefined &&
            this._datasource.deviceInformation.skuInformation !== null &&
            this._datasource.deviceInformation.skuInformation[key] !== undefined &&
            this._datasource.deviceInformation.skuInformation[key] !== null;
    }


    /**
     * Indicates if a given key is present in the sku information spec object.
     * @param {string} key The key to check.
     * @returns {boolean} True if the key exists, false otherwise.
     */
    hasSkuSpecKey(key)
    {
        return this._datasource.deviceInformation !== undefined &&
            this._datasource.deviceInformation !== null &&
            this._datasource.deviceInformation.skuInformation !== undefined &&
            this._datasource.deviceInformation.skuInformation !== null &&
            this._datasource.deviceInformation.skuInformation.spec_items !== undefined &&
            this._datasource.deviceInformation.skuInformation.spec_items !== null &&
            this._datasource.deviceInformation.skuInformation.spec_items[key] !== undefined &&
            this._datasource.deviceInformation.skuInformation.spec_items[key] !== null;
    }

    /**
     * Indicates if a given key is present in the localized resources object.
     * @param {string} key The key to check.
     * @returns {boolean} True if the key exists, false otherwise.
     */
    hasLocalizedResourceKey(key)
    {
        return this._datasource.localizedResources !== undefined &&
            this._datasource.localizedResources !== null &&
            this._datasource.localizedResources[key] !== undefined &&
            this._datasource.localizedResources[key] !== null;
    }

    /**
     * Gets a value from the registry object.
     * @param {string} key A key string to look in the registry object.
     * @returns {string|boolean} the value associated with the key, or false if the key doesn't exist.
     */
    getRegistryValue(key)
    {
        return this.hasRegistryKey(key) && this._datasource.deviceInformation.RetailInfo[key];
    }

    /**
     * Gets a value from the sku object.
     * @param {string} key A key string to look in the sku spec object.
     * @returns {string|boolean} the value associated with the key, or false if the key doesn't exist.
     */
    getSkuValue(key)
    {
        return this.hasSkuKey(key) && this._datasource.deviceInformation.skuInformation[key];
    }

    /**
     * Gets a value from the sku spec object.
     * @param {string} key A key string to look in the sku spec object.
     * @returns {string|boolean} the value associated with the key, or false if the key doesn't exist.
     */
    getSkuSpecValue(key)
    {
        return this.hasSkuSpecKey(key) && this._datasource.deviceInformation.skuInformation.spec_items[key];
    }

    /**
     * Gets a value from the localized resource object.
     * @param {string} key A key string to look in the localized resource object.
     * @returns {string|boolean} the value associated with the key, or false if the key doesn't exist.
     */
    getLocalizedResourceValue(key)
    {
        return this.hasLocalizedResourceKey(key) && this._datasource.localizedResources[key];
    }

    /**
     * Finds a particular promotion by its label.
     * @param {string} labelSearch  the label value to search for.
     */
    findOnPromotion(labelSearch)
    {
        for (var i = 1; i <= 4; i++)
        {
            const promotion = this.getSkuSpecValue(`promotion${i}`);

            if (promotion && promotion.label === labelSearch && promotion.short_description)
            {
                return promotion.short_description;
            }
        }

        return false;
    }
}

export const RdxDatasourceService = new RdxApplicationDatasourceService();