export const LogType = {
    Trace: 1,
    Debug: 2,
    Info: 3,
    Warning: 4,
    Error: 5,

    toString(type)
    {
        switch (type)
        {
            case LogType.Trace:
                return "TRACE";

            case LogType.Debug:
                return "DEBUG";

            case LogType.Info:
                return "INFO";

            case LogType.Warning:
                return "WARN";

            case LogType.Error:
                return "ERROR";

            default:
                throw Error("Log type not recognized.");
        }
    }
}

class ApplicationLoggingService
{
    constructor()
    {
        this.minimumLogLevel = LogType.Debug;
    }

    /**
     * Set the log provider.
     * @param {Object} logProvider the log provider.
     */
    setLogProvider(logProvider)
    {
        this.logProvider = logProvider;
    }

    /**
     * Returns the log provider.
     * @returns Returns a derived @see LogProvider
     */
    getLogProvider()
    {
        return this.logProvider;
    }

    /**
     * Set the minimum log level.
     * @param {LogType} type the type of log.
     */
    setMinimumLevel(logType)
    {
        this.minimumLogLevel = logType;
    }

    /**
     * Adds a trace log.
     * @param {string} message the message to be logged.
     * @param {Array} [optionalParams = null] the optional parameters array.
     */
    trace(message, optionalParams = null)
    {
        this.writeLog(LogType.Trace, message, optionalParams);
    }

    /**
     * Adds a debug log.
     * @param {string} message the message to be logged.
     * @param {Array} [optionalParams = null] the optional parameters array.
     */
    log(message, optionalParams = null)
    {
        this.writeLog(LogType.Debug, message, optionalParams);
    }

    /**
     * Adds an information log.
     * @param {string} message the message to be logged.
     * @param {Array} [optionalParams = null] the optional parameters array.
     */
    info(message, optionalParams = null)
    {
        this.writeLog(LogType.Info, message, optionalParams);
    }

    /**
     * Adds a warning log.
     * @param {string} message the message to be logged.
     * @param {Array} [optionalParams = null] the optional parameters array.
     */
    warn(message, optionalParams = null)
    {
        this.writeLog(LogType.Warning, message, optionalParams);
    }

    /**
     * Adds an error log.
     * @param {string} message the message to be logged.
     * @param {Array} [optionalParams = null] the optional parameters array.
     */
    error(message, optionalParams = null)
    {
        this.writeLog(LogType.Error, message, optionalParams);
    }

    /**
     * Writes the log.
     * @param {LogType} type the type of log.
     * @param {string} message the message to be logged.
     * @param {Array} [optionalParams = null] the optional parameters array.
     */
    writeLog(type, message, optionalParams)
    {
        if (!this.logProvider)
        {
            console.warn('logProvider was not initialized');
            return;
        }

        if (type < this.minimumLogLevel)
            return;

        const date = new Date();
        message = `[${date.toJSON()}][${ LogType.toString(type)}] - ${message}`;

        switch (type)
        {
            case LogType.Trace:
                this.logProvider.trace(message, optionalParams);
                break;

            case LogType.Debug:
                this.logProvider.log(message, optionalParams);
                break;

            case LogType.Info:
                this.logProvider.info(message, optionalParams);
                break;

            case LogType.Warning:
                this.logProvider.warn(message, optionalParams);
                break;

            case LogType.Error:
                this.logProvider.error(message, optionalParams);
                break;

            default:
                break;
        }
    }
}

export default new ApplicationLoggingService();