import React, { Component } from "react";
import PropTypes from "prop-types";
import NavSection from "./NavSection";
import styles from "./Navbar.module.scss";
import LocalizationService from "../../services/LocalizationService";

class Navbar extends Component {
    state = {
        activeSlide: null
    };

    setActiveSlide = slide => {
        this.setState({ activeSlide: slide });
    };

    render() {
        const { mobileMenuVisible } = this.state;
        const { getAccessibility } = LocalizationService;
        const arrowsDisabled = this.props.slides.length <= 1;

        const handleSectionClick = navigation => {
            const defaultSlideId = navigation.defaultSilde
                ? navigation.defaultSilde.id
                : navigation.slides[0].id;

            this.props.goToSlide(defaultSlideId);
        };

        return (
            <nav
                className={`${styles.navWrapper} ${this.props.theme === "dark" ? styles.darkTheme : ""}`}
                style={{
                    "--statusBarPosition": `${this.props.statusBarPosition}px`,
                    "--navAccentColor": this.props.accentColor
                }}
            >
                {!arrowsDisabled && (
                    <button
                        aria-label={getAccessibility("navigation.previous")}
                        className={styles.navArrow}
                        onClick={() => this.props.performNav("left")}
                    />
                )}

                <ul
                    ref={this.props.carouselNav}
                    className={`${styles.navList} ${mobileMenuVisible ? styles.isVisible : ""}`}
                >
                    {this.props.navigations.map((navigation, i) => (
                        <NavSection
                            key={i}
                            navigation={navigation}
                            currentSlide={this.props.currentSlide}
                            onSectionClick={handleSectionClick}
                            setActiveSlide={this.setActiveSlide}
                        />
                    ))}
                </ul>

                {!arrowsDisabled && (
                    <button
                        aria-label={getAccessibility("navigation.next")}
                        className={styles.navArrow}
                        onClick={() => this.props.performNav("right")}
                    />
                )}
            </nav>
        );
    }
}

Navbar.propTypes = {
    carouselNav: PropTypes.object.isRequired,
    slides: PropTypes.object.isRequired,
    currentSlide: PropTypes.string,
    navigations: PropTypes.array.isRequired,
    performNav: PropTypes.func.isRequired,
    goToSlide: PropTypes.func.isRequired,
    statusBarPosition: PropTypes.number.isRequired,
    theme: PropTypes.string.isRequired
};

export default Navbar;
