import { Translator } from "../translations/Translator";
import LoggingService from "./LoggingService";

class ApplicationConfigurationService
{
    /**
     * Loads the application model.
     * @returns {Promise<Application>} Returns a promise to obtain the application model.
     */
    async loadApplication()
    {
        await this.loadFile("/configuration/application.js").catch(e => console.error(e));
        return window.carouselApp;
    }

    /**
     * Loads the model and applies all the transformations.
     * @param {string} modelId The model identifier.
     * @param {string} languageId The language identifier.
     * @returns {Promise<{ navigation: Navigation, model: Model }>} Returns a promise to obtain the navigation and the model.
     */
    async loadModels(modelId, languageId)
    {
        // 1. loads the navigation file.
        await this.loadFile(`/configuration/navigations/${modelId}.js`).catch(e => console.error(e));
        const navigationDocument = window.carouselAppNavigations;

        // 2. loads the model file.
        await this.loadFile(`/configuration/models/${modelId}.js`).catch(e => console.error(e));
        const modelDocument = window.carouselAppModel;

        // 3. load devices
        await this.loadFile(`/configuration/devices/devices.js`);
        const devicesDocument = window.carouselAppDevices;

        // 4. loads the language file.
        let languageDocument = {};

        if (window.SDX &&
            window.SDX.modelConfiguration)
        {
            languageDocument = JSON.parse(window.SDX.modelConfiguration.surfaceFamily);
            LoggingService.info(`Loading localization from SDX with ${Object.keys(languageDocument).length} keys...`);
        }
        else
        {
            await this.loadFile(`/configuration/languages/${languageId}.js`).catch(e => console.error(e));
            languageDocument = window.carouselAppLanguage;
        }

        // 5. applies the translations.
        const translator = new Translator();
        translator.applyLanguage(modelDocument, languageDocument, modelId);
        translator.applyLanguage(navigationDocument.content, languageDocument, `${modelId}.navigation`);
        translator.applyLanguage(devicesDocument, languageDocument, `devices`);

        // 6. loads any overrides.
        await this.loadFile(`/configuration/overrides.js`).catch(e => console.error(e));

        return { navigation: navigationDocument, model: modelDocument, devices: devicesDocument };
    }

    /**
     * Loads a script file, and adds it as a document script.
     * @param {string} path Path to the js file.
     * @returns {Promise<void>} A promise.
     */
    async loadFile(path)
    {
        return new Promise((resolve, reject) =>
        {
            var script = document.createElement("script");
            script.onload = () => resolve();
            script.onerror = e => reject(e);
            script.src = process.env.PUBLIC_URL + path;
            document.body.appendChild(script);
        });
    }

    getSectionQuestions(sectionId)
    {
        const model = window.carouselAppModel.selectionSlide;
        const section = model.content.sections.find(section => section.id === sectionId);
        const questions = section.innerSlides.filter(slide => slide.type === "question");
        return questions;
    }
}

export const ConfigurationService = new ApplicationConfigurationService();
