import React, { useRef, useState, useEffect } from "react";
import PropTypes from "prop-types";
import useWindowWidth from "../../../../../../hooks/useWindowWidth";
import Disclaimer from "../../../../../SlideBlocks/Disclaimer";
import LaptopShowcase from "../../LaptopShowcase";
import styles from "./Mobile.module.scss";

const Mobile = ({
    devices,
    leftDevice,
    setLeftDevice,
    setRightDevice,
    rightDevice,
    handleClose,
    comparisonTable
}) => {
    const table = useRef();
    const [leftDisclaimers, setLeftDisclaimers] = useState(null);
    const [rightDisclaimers, setRightDisclaimers] = useState(null);
    const isIntermidiate = useWindowWidth(800);

    const setDisclaimers = device => {
        let disclaimers = [];

        device.specs.forEach(({ disclaimer }) => {
            const isArray = typeof disclaimer === "object";
            if (disclaimer) disclaimers.push(isArray ? disclaimer.join("\n") : disclaimer);
        });

        return disclaimers;
    };

    useEffect(() => {
        const wrapper = document.querySelector("[class*='animationWrapper']");
        wrapper.scrollTop = 0;

        setLeftDisclaimers(setDisclaimers(devices[leftDevice]));
        setRightDisclaimers(setDisclaimers(devices[rightDevice]));
    }, [devices, leftDevice, rightDevice]);

    return (
        <div ref={table} className={styles.comparisonTableMobile}>
            <div className={styles.tableHeader}>
                <button className={styles.backButton} onClick={handleClose}>
                    <i className="ms-Icon ms-Icon--ChevronLeft" />
                    {comparisonTable.backBtn}
                </button>

                {isIntermidiate && (
                    <>
                        {/* Left device */}
                        <LaptopShowcase
                            laptops={devices}
                            current={leftDevice}
                            currentComparison={rightDevice}
                            setCurrentDevice={setLeftDevice}
                            showDisclaimers={false}
                        />

                        {/* Right device */}
                        <LaptopShowcase
                            laptops={devices}
                            current={rightDevice}
                            currentComparison={leftDevice}
                            setCurrentDevice={setRightDevice}
                            showDisclaimers={false}
                        />
                    </>
                )}

                {!isIntermidiate && (
                    <>
                        {/* Left device */}
                        <div className={styles.headerLaptop}>
                            <img
                                src={devices[leftDevice].colors[0].mainImage}
                                alt={devices[leftDevice].name}
                            />
                            <h2>{devices[leftDevice].name}</h2>
                        </div>

                        {/* Right device */}
                        <div className={styles.headerLaptop}>
                            <img
                                src={devices[rightDevice].colors[0].mainImage}
                                alt={devices[rightDevice].name}
                            />
                            <h2>{devices[rightDevice].name}</h2>
                        </div>
                    </>
                )}
            </div>

            <div className={styles.tableContent} key={`${devices[leftDevice].name} vs ${devices[rightDevice].name}`}>
                {comparisonTable.specsToShow.map((spec, i) => (
                    <div className={styles.tableRow} key={spec.id}>
                        <h3>{spec.name}</h3>
                        <p>{devices[leftDevice].specs[i].paragraph}</p>
                        <p>{devices[rightDevice].specs[i].paragraph}</p>
                    </div>
                ))}

                {leftDisclaimers && rightDisclaimers && (
                    <div className={styles.tableRow}>
                        <Disclaimer disclaimers={leftDisclaimers} />
                        <Disclaimer disclaimers={rightDisclaimers} />
                    </div>
                )}
            </div>
        </div>
    );
};

Mobile.propTypes = {
    devices: PropTypes.array.isRequired,
    leftDevice: PropTypes.number.isRequired,
    rightDevice: PropTypes.number.isRequired,
    setLeftDevice: PropTypes.func.isRequired,
    setRightDevice: PropTypes.func.isRequired,
    comparisonTable: PropTypes.object.isRequired,
    handleClose: PropTypes.func.isRequired
};

export default Mobile;
