import React, { Component } from "react";
import PropTypes from "prop-types";
import BrowserDetectionService from "../../services/BrowserDetectionService";
import TelemetryService from "../../services/TelemetryService";
import SlideSwitch from "../SlideSwitch";
import Navbar from "../Navbar";
import styles from "./Carousel.module.scss";

class Carousel extends Component
{
    state = {
        currentSlide: null,
        animationInProgress: null,
        statusBarPosition: 0
    };
    carouselMain = React.createRef();
    carouselNav = React.createRef();
    slideOffset = 0;
    isSwiping = false;
    swipeThreshold = this.props.swipeThreshold || 75;
    delay = 400;
    leavingSlide = null;
    upcomingSlide = null;
    pointerStart;
    pointerEnd;
    previousLeft = 0;
    animationDirection;
    isSafari;
    carouselTimer = new Date();

    componentDidMount()
    {
        this.isSafari = BrowserDetectionService.isSafari();

        window.addEventListener("keydown", this.handleKeyDown);
        window.addEventListener("resize", this.updateStatusBar);
        document.body.addEventListener("pointerleave", this.handlePointerEnd);

        this.setInitialSlide();
    }

    componentDidUpdate(_, prevState)
    {
        if (prevState.currentSlide !== this.state.currentSlide)
        {
            this.setAnimation("enter");
            this.updateStatusBar();
            this.updateSlidePosition(0);
        }
    }

    componentWillUnmount()
    {
        window.removeEventListener("keydown", this.handleKeyDown);
        window.removeEventListener("resize", this.updateStatusBar);
        document.body.removeEventListener("pointerleave", this.handlePointerEnd);
        this.stopCarouselTimer();
    }

    stopCarouselTimer = () =>
    {
        let carouselEndTimer = new Date();
        let timeDifference = carouselEndTimer.getTime() - this.carouselTimer.getTime();

        TelemetryService.timeCarousel(timeDifference);
    };

    handlePointerStart = e =>
    {
        if (!this.props.canSwipe) return;

        this.isSwiping = true;
        this.pointerStart = e.clientX;
        const delta = this.getPositionDelta(e);

        if (window.SDX && window.SDX.swipeStart)
        {
            window.SDX.swipeStart(e.clientX, delta);
        }
    };

    handlePointerMove = e =>
    {
        if (!this.isSwiping) return;

        let delta = this.getPositionDelta(e);
        this.updateSlidePosition(this.slideOffset + delta);

        if (window.SDX && window.SDX.swipeContinue)
        {
            window.SDX.swipeContinue(e.clientX, delta);
        }
    };

    handlePointerEnd = e =>
    {
        if (!this.isSwiping) return;

        this.pointerEnd = e.clientX;
        this.isSwiping = false;
        this.handleSwipeEvent();

        if (window.SDX && window.SDX.swipeEnd)
        {
            window.SDX.swipeEnd(e.clientX, e.clientX - this.previousLeft);
        }
    };

    getPositionDelta = e =>
    {
        const currentLeft = e.clientX;
        const delta = currentLeft - this.previousLeft;

        this.previousLeft = currentLeft;
        return delta;
    };

    handleSwipeEvent = () =>
    {
        if (Math.abs(this.pointerStart - this.pointerEnd) >= this.swipeThreshold)
        {
            const direction = this.pointerStart >= this.pointerEnd ? "right" : "left";
            this.performNav(direction);
        } else
        {
            this.updateSlidePosition(0);
        }
    };

    handleKeyDown = e =>
    {
        if (e.keyCode === 37)
        {
            this.performNav("left");
        } else if (e.keyCode === 39)
        {
            this.performNav("right");
        }
    };

    performNav = direction =>
    {
        const { slides } = this.props;
        let currentSlideIndex = this.getSlideIndex(this.state.currentSlide);
        let upcomingIndex = currentSlideIndex;

        if (direction === "left")
        {
            if (currentSlideIndex < 1) upcomingIndex = slides.length;
            --upcomingIndex;
        } else if (direction === "right")
        {
            if (currentSlideIndex === slides.length - 1) upcomingIndex = -1;
            ++upcomingIndex;
        }

        const upcoming = slides[upcomingIndex];

        if (upcoming)
        {
            this.goToSlide(upcoming.config.id);
        } else
        {
            this.updateSlidePosition(0);
        }
    };

    setInitialSlide = () =>
    {
        let initialIndex = this.getSlideIndex(this.props.initialSlide.id);
        this.setState({ currentSlide: this.getSlideId(initialIndex > -1 ? initialIndex : 0) });
    };

    updateSlidePosition = offset =>
    {
        if (!this.carouselMain || !this.carouselMain.current) return;

        this.slideOffset = offset;
        this.carouselMain.current.style.setProperty("--currentPosition", `${this.slideOffset}px`);
    };

    getSlideIndex = slideId =>
    {
        const ids = Object.keys(this.props.slides);
        return ids.indexOf(slideId);
    };

    getSlideId = index =>
    {
        const ids = Object.keys(this.props.slides);
        return ids[index];
    };

    goToSlide = slideId =>
    {
        this.leavingSlide = this.state.currentSlide;
        this.upcomingSlide = slideId;
        if (this.leavingSlide !== this.upcomingSlide) this.setAnimation("leave");
        window.setTimeout(() => this.setState({ currentSlide: slideId }), this.delay - 50);
    };

    setAnimation = animState =>
    {
        const direction = this.getDirection();
        const animationDirection = direction ? "rtl" : "ltr";
        let leaveToPosition;

        if (animState === "leave")
        {
            animationDirection === "rtl"
                ? (leaveToPosition = this.slideOffset - 100)
                : (leaveToPosition = this.slideOffset + 100);

            this.carouselMain.current.style.setProperty(
                "--leaveToPosition",
                `${leaveToPosition}px`
            );
        }

        this.setState({
            animationInProgress: `${animState}-${animationDirection}`
        });
    };

    getDirection = () =>
    {
        const { slides } = this.props;
        let previousIndex = this.getSlideIndex(this.leavingSlide);
        let upcomingIndex = this.getSlideIndex(this.upcomingSlide);

        if (previousIndex === slides.length - 1 && upcomingIndex === 0) return true;
        else if (previousIndex === 0 && upcomingIndex === slides.length - 1) return false;

        return previousIndex < upcomingIndex;
    };

    updateStatusBar = () =>
    {
        if (this.props.slides.length <= 1) return;

        let activeElement;

        if (this.carouselNav.current)
        {
            // eslint-disable-next-line no-unused-vars
            for (let child of this.carouselNav.current.childNodes)
            {
                if (child.className.indexOf("active") >= 0)
                {
                    activeElement = child;
                }
            }

            this.setState({
                statusBarPosition: activeElement.getBoundingClientRect().right
            });
        }
    };

    render()
    {
        const current = this.props.slides[this.state.currentSlide];
        const swipeEvents = this.isSafari
            ? {
                onMouseDown: this.handlePointerStart,
                onMouseMove: this.handlePointerMove,
                onMouseUp: this.handlePointerEnd
            }
            : {
                onPointerDown: this.handlePointerStart,
                onPointerMove: this.handlePointerMove,
                onPointerUp: this.handlePointerEnd
            };

        return (
            <>
                {current && (
                    <main
                        ref={this.carouselMain}
                        className={`${styles.main} ${this.state.animationInProgress} ${this.props.navDisabled ? styles.navDisabled : ""}`}
                        style={{
                            "--delay": `${this.delay}ms`,
                            "--transitionDirection": this.getDirection() ? "25px" : "-25px"
                        }}
                        aria-live="assertive"
                        aria-controls="slideSection"
                        {...swipeEvents}
                    >
                        <section id="slideSection">
                            <SlideSwitch
                                slide={current}
                                slideSection={this.props.initialSlide.section}
                            />
                        </section>
                    </main>
                )}

                {!this.props.navDisabled && (
                    <Navbar
                        carouselNav={this.carouselNav}
                        slides={this.props.slides}
                        currentSlide={this.state.currentSlide}
                        navigations={this.props.navigations}
                        performNav={this.performNav}
                        goToSlide={this.goToSlide}
                        statusBarPosition={this.state.statusBarPosition}
                        theme={this.props.theme}
                    />
                )}
            </>
        );
    }
}

Carousel.propTypes = {
    slides: PropTypes.object.isRequired,
    navigations: PropTypes.array.isRequired,
    initialSlide: PropTypes.object.isRequired,
    swipeThreshold: PropTypes.number,
    theme: PropTypes.string,
    navDisabled: PropTypes.bool.isRequired
};

export default Carousel;
