import React, { useState, useEffect } from "react";
import { SwitchTransition, CSSTransition } from "react-transition-group";
import PropTypes from "prop-types";
import useEventListener from "../../../hooks/useEventListener";
import styles from "./Laptop.module.scss";

const colorTransitionDelay = 250;

const Laptop = ({ laptop, tableVisible }) => {
    const [height, setHeight] = useState(0);
    const [currentColor, setCurrentColor] = useState(0);

    const setContainerHeight = () => {
        const laptopImageElements = document.querySelectorAll(".laptopImage");
        laptopImageElements.forEach(image => {
            if (image.clientHeight !== 0) setHeight(image.clientHeight);
        });
    };

    useEventListener("resize", setContainerHeight);
    useEffect(setContainerHeight, [tableVisible]);

    return (
        <div
            className={styles.laptop}
            style={{ "--colorTransitionDelay": `${colorTransitionDelay}ms` }}
        >
            <div className={styles.imageContainer} style={{ height }}>
                <SwitchTransition mode="in-out">
                    <CSSTransition
                        key={laptop.colors[currentColor].color}
                        timeout={colorTransitionDelay}
                        classNames={{
                            enter: styles.imageEnter,
                            enterActive: styles.imageEnterActive,
                            exit: styles.imageExit,
                            exitActive: styles.imageExitActive
                        }}
                    >
                        <img
                            className="laptopImage"
                            src={laptop.colors[currentColor].mainImage}
                            alt={`${laptop.name} thumbnail`}
                            onLoad={setContainerHeight}
                        />
                    </CSSTransition>
                </SwitchTransition>
            </div>

            <div className={styles.colorSelector}>
                {laptop.colors.map(({ color }, i) => (
                    <button
                        key={i}
                        className={currentColor === i ? styles.isCurrent : ""}
                        style={{ backgroundColor: color }}
                        onClick={() => setCurrentColor(i)}
                    />
                ))}
            </div>

            <h1>{laptop.name}</h1>
            <p>{laptop.text}</p>
        </div>
    );
};

Laptop.propTypes = {
    laptop: PropTypes.object.isRequired,
    tableVisible: PropTypes.bool
};

export default Laptop;
