import { PlatformDetectionService } from "../services/PlatformDetectionService";
import { RdxDatasourceService } from "../services/RdxDatasourceService";
import { guid } from "../shared/guid";

export class DeviceInformation {
    static get appCodeName() { return navigator.appCodeName; }

    static get appName() { return navigator.appName; }

    static get appVersion() { return navigator.appVersion; }

    static get cookieEnabled() {
        return (navigator.cookieEnabled !== null && navigator.cookieEnabled !== undefined)
            ? navigator.cookieEnabled.toString()
            : 'not defined';
    }

    static get language() { return navigator.language; }

    static get onLine() {
        return (navigator.onLine !== null && navigator.onLine !== undefined)
            ? navigator.onLine.toString()
            : 'not defined';
    }

    static get platform() { return navigator.platform; }

    static get userAgent() { return navigator.userAgent; }

    static get origin() {
        let origin;

        if (PlatformDetectionService.isUwp()) {
            if (window.SDX)
                origin = "SDX";
            else if (window.RDX || window.parent?.RDX)
                origin = "RDA";
        } else {
            origin = "WEB";
        }

        return origin;
    }
}

export class RdxDeviceInformation extends DeviceInformation {
    static get retailAccessCode() { return RdxDatasourceService.getRegistryValue("RetailAccessCode"); }
    static get Region() { return RdxDatasourceService.getRegistryValue("Region"); }
    static get StoreID() { return RdxDatasourceService.getRegistryValue("StoreID"); }
    static get SystemSKU() { return RdxDatasourceService.getRegistryValue("SystemSKU"); }
    static get SKU() { return RdxDatasourceService.getRegistryValue("SKU"); }
    static get MachineId() { return RdxDatasourceService.getRegistryValue("MachineId"); }
}

export class webDeviceInformation extends DeviceInformation {
    static setDefaultProperties() {
        const queryParams = new URLSearchParams(window.location.search);
        let urlParams = ["RAC", "StoreID", "SKU", "Source"];
        urlParams.forEach(property => {
            this[property] = queryParams.get(property)
        });
        //Set sessionId
        this.guid = guid();
    }

    static get retailAccessCode() { return this.RAC; }
    static get storeID() { return this.StoreID; }
    static get sku() { return this.SKU; }
    static get sourceId() { return this.Source; }
    static get sessionId() { return this.guid; }
}