import React, { useState, useRef, Fragment, useEffect, useCallback } from "react";
import PropTypes from "prop-types";
import HelpMeChooseService from "../../../../../services/HelpMeChooseService";
import useWindowWidth from "../../../../../hooks/useWindowWidth";
import useEventListener from "../../../../../hooks/useEventListener";
import Question from "../QuestionsSwitch/Question";
import Progress from "./Progress";
import Navigation from "./Navigation";
import CSSService from "../../../../../services/CSSService";

const boxThreshold = 15;

const QuestionsSwitch = ({ setShowResults, setShowLoading, delay }) => {
    const question = useRef();
    const navigation = useRef();
    const [navOverlapping, toggleNavOverlapping] = useState(false);
    const [, forceUpdate] = useState();
    const [currentQuestion, setCurrentQuestion] = useState(HelpMeChooseService.getCurrentQuestion());
    const isDesktop = useWindowWidth(800);

    const selectOption = option => {
        HelpMeChooseService.setSelection(option);
        forceUpdate({});
    };

    const handleResize = useCallback(() => {
        const { current: questionRef } = question;
        const { current: navRef } = navigation;
        const questionBottom = questionRef.getBoundingClientRect().bottom;
        const navTop = navRef.getBoundingClientRect().top;

        if (questionBottom + boxThreshold > navTop) {
            toggleNavOverlapping(true);
            const maxHeight = questionRef.clientHeight - (questionBottom - navTop);
            CSSService.setCustomProperty("overlapContainerHeight", `${maxHeight}px`, questionRef);
        } else {
            toggleNavOverlapping(false);
        }
    }, []);

    useEffect(handleResize, [selectOption]);
    useEventListener("resize", handleResize);

    return (
        <>
            {isDesktop && <Progress {...{ currentQuestion }} />}

            <Fragment key={currentQuestion.id}>
                <Question ref={question} {...{ currentQuestion, selectOption, navOverlapping }} />
                <Navigation ref={navigation} {...{ setCurrentQuestion, setShowResults, setShowLoading, delay }} />
            </Fragment>
        </>
    );
};

QuestionsSwitch.propTypes = {
    setShowResults: PropTypes.func.isRequired,
    setShowLoading: PropTypes.func.isRequired,
    delay: PropTypes.number.isRequired
};

export default QuestionsSwitch;
