import React, { forwardRef, useEffect, useState, useCallback } from "react";
import PropTypes from "prop-types";
import HelpMeChooseService from "../../../../../../services/HelpMeChooseService";
import useEventListener from "../../../../../../hooks/useEventListener";
import Heading from "../../../../../SlideBlocks/Heading";
import OptionCard from "../../../../../SlideBlocks/OptionCard";
import SlideBackground from "../../../../../SlideBlocks/SlideBackground";
import OptionsWrapper from "./OptionsWrapper";
import Slider from "./Slider";
import styles from "./Question.module.scss";

const scrollbarThreshold = 5;
const setNeedScrollbar = el => el.clientHeight + scrollbarThreshold < el.scrollHeight;

const Question = forwardRef(({ selectOption, currentQuestion, navOverlapping }, ref) => {
    const [isEndOfScroll, setIsEndOfScroll] = useState(false);
    const [scrollbarVisible, setScrollbarVisibility] = useState(false);
    const [scrollPosition, setScrollPosition] = useState(0);
    const currentSelections = HelpMeChooseService.getCurrentSelection();
    const questionClassName = `question_${currentQuestion.id}`;
    const checkIfSelected = id => currentSelections.indexOf(id) !== -1;

    const checkEndOfScroll = ({ scrollHeight, scrollTop, clientHeight }) => {
        if (!navOverlapping) return;
        setIsEndOfScroll(scrollHeight - scrollTop < clientHeight);
        setScrollbarVisibility(setNeedScrollbar(ref?.current));
    };

    const setScrollbarPosition = useCallback(
        target => {
            if (!navOverlapping) return;
            const scroll = target.scrollTop;
            const height = target.scrollHeight - target.clientHeight;
            const position = (scroll / height) * 100;
            setScrollPosition(position);
        },
        [navOverlapping]
    );

    useEffect(() => {
        setScrollbarPosition(ref?.current);
        setScrollbarVisibility(setNeedScrollbar(ref?.current));
    }, [ref, setScrollbarPosition]);

    useEffect(() => {
        const wrapper = ref?.current?.parentElement;
        wrapper.scrollTop = 0;
    }, [currentQuestion, ref])

    useEventListener("resize", checkEndOfScroll);

    return (
        <>
            {currentQuestion.background && (
                <SlideBackground background={currentQuestion.background} />
            )}

            <div
                className={`
                    ${styles.question}
                    ${styles[questionClassName]}
                `}
            >
                <Heading
                    title={currentQuestion.title}
                    subtitle={currentQuestion.subTitle}
                    chooseLimit={currentQuestion.chooseLimit}
                />

                <div
                    {...{ ref }}
                    className={`
                        ${styles.overlapContainer}
                        ${navOverlapping ? styles.isOverlapping : ""}
                        ${navOverlapping && !isEndOfScroll ? styles.overlayVisible : ""}
                    `}
                    onScroll={({ target }) => {
                        checkEndOfScroll(target);
                        setScrollbarPosition(target);
                    }}
                >
                    {navOverlapping && scrollbarVisible && (
                        <div
                            className={styles.scrollbar}
                            style={{
                                "--containerScrollbarPos": `${scrollPosition}%`,
                                left: ref.current.offsetLeft
                            }}
                        />
                    )}

                    {!currentQuestion.hasSlider && (
                        <OptionsWrapper>
                            {currentQuestion.options.map(option => (
                                <OptionCard
                                    key={`option-${option.id}`}
                                    option={option}
                                    selected={checkIfSelected(option.id)}
                                    handleOnClick={() => selectOption(option)}
                                />
                            ))}
                        </OptionsWrapper>
                    )}
                </div>

                {currentQuestion.hasSlider && (
                    <Slider
                        options={currentQuestion.options}
                        currentSelection={currentSelections[0]}
                        handleSelection={selectOption}
                    />
                )}
            </div>
        </>
    );
});

Question.propTypes = {
    selectOption: PropTypes.func.isRequired,
    currentQuestion: PropTypes.object.isRequired,
    navOverlapping: PropTypes.bool.isRequired
};

export default Question;
