import React from "react";
import PropTypes from "prop-types";
import useWindowWidth from "../../../../../hooks/useWindowWidth";
import Desktop from "./Desktop";
import Mobile from "./Mobile";

const ComparisonTable = ({
    devices,
    leftDevice,
    rightDevice,
    setLeftDevice,
    setRightDevice,
    comparisonTable,
    showMore,
    handleClose,
    toggleShowMore
}) => {
    const isDesktop = useWindowWidth(1300);

    return (
        <>
            {isDesktop && (
                <Desktop
                    {...{
                        devices,
                        leftDevice,
                        rightDevice,
                        comparisonTable,
                        showMore,
                        handleClose,
                        toggleShowMore
                    }}
                />
            )}

            {!isDesktop && (
                <Mobile
                    {...{
                        devices,
                        leftDevice,
                        rightDevice,
                        setLeftDevice,
                        setRightDevice,
                        handleClose,
                        comparisonTable
                    }}
                />
            )}
        </>
    );
};

ComparisonTable.propTypes = {
    devices: PropTypes.array.isRequired,
    leftDevice: PropTypes.number.isRequired,
    rightDevice: PropTypes.number.isRequired,
    setLeftDevice: PropTypes.func.isRequired,
    setRightDevice: PropTypes.func.isRequired,
    comparisonTable: PropTypes.object.isRequired,
    showMore: PropTypes.bool.isRequired,
    handleClose: PropTypes.func.isRequired,
    toggleShowMore: PropTypes.func.isRequired
};

export default ComparisonTable;
