import React, { useState, useEffect, useRef, useCallback } from "react";
import PropTypes from "prop-types";
import useEventListener from "../../../hooks/useEventListener";
import styles from "./ScrollBox.module.scss";

const ScrollBox = ({ children, threshold }) => {
    const [scrollPos, setScrollPos] = useState(0);
    const [needsScrollbar, setNeedsScrollbar] = useState(false);
    const scrollBox = useRef();

    const checkBoxHeight = useCallback(() => {
        if (scrollBox.current.clientHeight > threshold) {
            setNeedsScrollbar(true);
        } else if (scrollBox.current.scrollHeight - 5 <= scrollBox.current.clientHeight) {
            setNeedsScrollbar(false);
        }
    }, [threshold]);

    function handleScrollPosition({ target }) {
        let scroll = target.scrollTop;
        let height = target.scrollHeight - target.clientHeight;
        setScrollPos((scroll / height) * 100);
    }

    useEffect(() => {
        checkBoxHeight();
    }, [children, checkBoxHeight]);

    useEventListener("resize", checkBoxHeight);

    return (
        <div
            className={`${styles.scrollBox} ${needsScrollbar ? styles.hasScrollbar : ""}`}
            onPointerDown={e => e.stopPropagation()}
            onScroll={handleScrollPosition}
            style={{ "--scrollBoxPosition": `${scrollPos}%` }}
        >
            <div ref={scrollBox}>{children}</div>
        </div>
    );
};

ScrollBox.propTypes = {
    children: PropTypes.node.isRequired,
    threshold: PropTypes.number.isRequired
};

export default ScrollBox;
