export class PlatformDetectionService {
    /**
     * Checks if the platform is desktop
     */
    static isDesktop = () => window.matchMedia(`(min-width: ${800}px)`).matches;

    /**
     * Checks if the platform is UWP app
     */
    static isUwp = () => window.SDX || window.RDX || window.parent?.RDX;
}