import React, { Component, createRef } from "react";
import PropTypes from "prop-types";
import CSSService from "../../../../../../../services/CSSService";
import AnimationService from "../../../../../../../services/AnimationService";
import styles from "./Slider.module.scss";

const compareFn = position => (a, b) => {
    return Math.abs(a.relativePosition - position) - Math.abs(b.relativePosition - position);
};

class Slider extends Component {
    dragOffset = 0;
    isDragging = false;
    isAnimating = false;
    slider = createRef();
    sliderMarker = createRef();
    sliderImage = createRef();
    currentImage = createRef();
    nextImage = createRef();
    sliderDotsRefs = [];
    sliderDotsRefsInitialized = false;
    offsetX = 0;
    currentDots = {
        actual: null,
        next: null
    };

    componentDidMount() {
        this.getSliderDotsRefs();
        this.setListeners();
        this.handleTransitionStyling(0);

        if (this.props.currentSelection) {
            const selectedOption = this.getDotOption(this.props.currentSelection);

            if (selectedOption)
                this.selectOption(selectedOption.relativePosition, selectedOption.option, 0);
        } else {
            this.selectOption(0, this.props.options[0], 0);
        }
    }

    componentWillUnmount() {
        this.setListeners(true);
    }

    animateMarker = (fromPosition, toPosition, duration = 700) => {
        const easeing = t => (t < 0.5 ? 16 * t * t * t * t * t : 1 + 16 * --t * t * t * t * t);

        return new Promise((resolve, _) => {
            AnimationService.animate(p => {
                p = easeing(p);
                const markerPosition = AnimationService.lerp(fromPosition, toPosition, p);
                this.setMarkerPosition(markerPosition);
                this.calc(markerPosition);

                if (p === 1) resolve();
            }, Math.abs(toPosition - fromPosition) * duration);
        });
    };

    selectOption = async (relativePosition, option, duration = undefined) => {
        if (this.isDragging || this.isAnimating) return;

        const fromPosition = this.offsetX;
        const toPosition = relativePosition;

        this.isAnimating = true;
        await this.animateMarker(fromPosition, toPosition, duration);
        this.isAnimating = false;
        this.isDragging = false;
        this.props.handleSelection(option);
    };

    handlePointerDown = e => {
        e.stopPropagation();
        this.dragOffset = e.clientX;
        this.isDragging = true;
    };

    handlePointerMove = e => {
        e.stopPropagation();

        if (!this.isDragging) return;

        const delta = e.clientX - this.dragOffset;
        let position = this.offsetX + this.getRelativePosition(delta);

        if (position < 0) position = 0;
        if (position > 1) position = 1;
        
        this.calc(position);
        this.setMarkerPosition(position);
        this.dragOffset = e.clientX;
    };

    handlePointerUp = async () => {
        if (!this.isDragging) return;

        const fromPosition = this.offsetX;
        const [first] = this.getNearestDots(fromPosition);
        const toPosition = first.relativePosition;

        this.isDragging = false;
        await this.animateMarker(fromPosition, toPosition);
        this.props.handleSelection(first.option);
    };

    handleResize = () => {
        this.setMarkerPosition(this.offsetX);
    };

    calc = position => {
        const [first, second] = this.getNearestDots(position);

        this.handleTransitionStyling(position);

        if (
            (this.currentDots.current === first && this.currentDots.next === second) ||
            (this.currentDots.current === second && this.currentDots.next === first)
        )
            return;

        this.setDots(first, second);
        this.handleTransitionStyling(position);
    };

    handleTransitionStyling = position => {
        this.setScaleValues(position);
        this.setOpacityValues(position);
    };

    setOpacityValues = position => {
        const { current, next } = this.currentDots;
        const p = (position - current.relativePosition) / (next.relativePosition - current.relativePosition);
        const pN = 1 - p;

        CSSService.setCustomProperty("opacityActive", pN, this.sliderImage.current);
        CSSService.setCustomProperty("opacityNext", p, this.sliderImage.current);
    };

    setScaleValues = position => {
        if (
            !this.currentDots?.current?.option?.imageInitialScale ||
            !this.currentDots?.next?.option?.imageInitialScale
        )
            return;

        const { current, next } = this.currentDots;
        const p = (position - current.relativePosition) / (next.relativePosition - current.relativePosition);
        const { x: x1, y: y1 } = current.option.imageInitialScale;
        const { x: x2, y: y2 } = next.option.imageInitialScale;
        const sx = AnimationService.lerp(x1, x2, p);
        const sy = AnimationService.lerp(y1, y2, p);

        CSSService.setCustomProperty("scaleX", sx, this.sliderImage.current);
        CSSService.setCustomProperty("scaleY", sy, this.sliderImage.current);
    };

    getRelativePosition = position => {
        return position / this.getSliderTotalWidth();
    };

    getSliderTotalWidth = () => {
        const computedStyle = getComputedStyle(this.sliderDotsRefs[0].node);
        const sliderDotWidth = parseFloat(computedStyle.getPropertyValue("--sliderDotWidth"));
        return this.slider.current.offsetWidth - sliderDotWidth;
    };

    getSliderDotsRefs = () => {
        const totalWidth = this.getSliderTotalWidth();
        this.sliderDotsRefs.forEach(x => (x.relativePosition = x.node.offsetLeft / totalWidth));
    };

    getNearestDots = position => {
        return this.sliderDotsRefs.sort(compareFn(position)).slice(0, 2);
    };

    getDotOption = id => {
        return this.sliderDotsRefs.find(x => x.option.id === id);
    };

    setMarkerPosition = position => {
        const p = position * this.getSliderTotalWidth();

        CSSService.setCustomProperty("markerPosition", `${p}px`, this.sliderMarker.current);
        this.offsetX = position;
    };

    setDots = (current, next, callback) => {
        this.currentDots = { current, next };
        this.setState({ ...this.state }, callback);
    };

    setListeners = (remove = false) => {
        document.body.addEventListener("pointermove", this.handlePointerMove);
        document.body.addEventListener("pointerup", this.handlePointerUp);
        document.body.addEventListener("pointerleave", this.handlePointerUp);
        window.addEventListener("resize", this.handleResize);

        if (remove) {
            document.body.removeEventListener("pointermove", this.handlePointerMove);
            document.body.removeEventListener("pointerup", this.handlePointerUp);
            document.body.removeEventListener("pointerleave", this.handlePointerUp);
            window.removeEventListener("resize", this.handleResize);
        }
    };

    renderScreens = () => {
        const { current } = this.currentDots;

        if (!current || !current.option.image) return null;

        return (
            <div>
                <img
                    className={styles.current}
                    ref={this.currentImage}
                    src={this.currentDots.current.option.image}
                    alt={this.currentDots.current.option.id}
                />

                <img
                    className={styles.next}
                    ref={this.nextImage}
                    src={this.currentDots.next.option.image}
                    alt={this.currentDots.next.option.id}
                />
            </div>
        );
    };

    handleSliderDotsInfo = (option, node) => {
        if (!this.sliderDotsRefsInitialized) {
            this.sliderDotsRefs.push({ node: node, option });

            if (this.sliderDotsRefs.length === this.props.options.length) {
                this.sliderDotsRefsInitialized = true;

                // Once all the node were created we save the current and next dots
                this.setDots(this.sliderDotsRefs[0], this.sliderDotsRefs[1]);
            }
        }
    };

    render() {
        const currentSelection = this.props.options.filter(
            ({ id }) => id === this.props.currentSelection
        )[0];

        return (
            <>
                <div ref={this.slider} className={styles.slider}>
                    {this.props.options.map(option => (
                        <button
                            key={`option-${option.id}`}
                            ref={node => this.handleSliderDotsInfo(option, node)}
                            className={styles.sliderOption}
                            onPointerDown={e =>
                                this.selectOption(
                                    this.getRelativePosition(e.target.offsetLeft),
                                    option
                                )
                            }
                        >
                            <div className={styles.sliderOptionText}>
                                <p>{option.text}</p>
                                {option.detail && <span>{option.detail}</span>}
                            </div>
                        </button>
                    ))}

                    <button
                        ref={this.sliderMarker}
                        className={styles.sliderMarker}
                        onPointerDown={this.handlePointerDown}
                    />
                </div>

                {currentSelection && (
                    <div className={styles.sliderCurrentText}>
                        <p>{currentSelection.text}</p>
                        {currentSelection.detail && <small>{currentSelection.detail}</small>}
                    </div>
                )}

                <div ref={this.sliderImage} className={styles.sliderImage}>
                    {this.renderScreens()}
                </div>
            </>
        );
    }
}

Slider.propTypes = {
    options: PropTypes.array.isRequired,
    currentSelection: PropTypes.string,
    handleSelection: PropTypes.func.isRequired
};

export default Slider;
