import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import useSectionTimeTelemetry from "../../../../hooks/useSectionTimeTelemetry";
import CSSService from "../../../../services/CSSService";
import Loading from "./Loading";
import Results from "./Results";
import QuestionsSwitch from "./QuestionsSwitch";
import HelpMeChooseService from "../../../../services/HelpMeChooseService";

const loadingDelay = 10000;
CSSService.setCustomProperty("loadingDelay", `${loadingDelay}ms`);

const HelpMeChooseSection = ({ loadingSlide, resultsSlide, queriedResult }) => {
    const [showResults, setShowResults] = useState(queriedResult);
    const [showLoading, setShowLoading] = useState(false);
    const [result, setQueriedResult] = useState(null);

    const startOver = () => {
        HelpMeChooseService.startOver();
        setShowResults(false);
    };

    useEffect(() => {
        if (!queriedResult) return;
        const devices = HelpMeChooseService.getDevices();
        const selectedDevice = devices.filter(x => x.id === queriedResult);
        setQueriedResult(selectedDevice[0]);
        setShowResults(true);
    }, [queriedResult]);

    useSectionTimeTelemetry("Help Me Choose");

    return (
        <>
            {!showResults && (
                <QuestionsSwitch {...{ setShowResults, setShowLoading }} delay={loadingDelay} />
            )}

            {showResults && (
                <>
                    {showLoading && <Loading data={loadingSlide} delay={loadingDelay} />}
                    {!showLoading && <Results data={resultsSlide} startOver={startOver} device={result} />}
                </>
            )}
        </>
    );
};

HelpMeChooseSection.propTypes = {
    loadingSlide: PropTypes.object.isRequired,
    resultsSlide: PropTypes.object.isRequired
};

export default HelpMeChooseSection;
