import React, { useRef } from "react";
import PropTypes from "prop-types";
import { CSSTransition, SwitchTransition } from "react-transition-group";
import CSSService from "../../../../../services/CSSService";
import useWindowWidth from "../../../../../hooks/useWindowWidth";
import Disclaimer from "../../../../SlideBlocks/Disclaimer";
import Laptop from "../../../../SlideBlocks/Laptop";
import styles from "./LaptopShowcase.module.scss";

const swipeThreshold = 75;
const revealOffset = 150;
const revealTimeout = 300;
let isDraging = false;

const LaptopShowcase = ({
    laptops,
    current,
    currentComparison,
    disclaimersToRight,
    setCurrentDevice,
    showDisclaimers,
    tableVisible
}) => {
    const laptopShowcaseRef = useRef();
    const currentOffset = useRef();
    const dragOffset = useRef(0);
    const isDesktop = useWindowWidth(1300);

    const handlePointerDown = e => {
        e.stopPropagation();
        isDraging = true;
        dragOffset.current = isDesktop ? e.clientY : e.clientX;
    };

    const handlePointerUp = e => {
        if (!isDraging) return;

        isDraging = false;

        const position = isDesktop ? e.clientY : e.clientX;
        const tapped = position === dragOffset.current;
        const reachedThreshold = Math.abs(position - dragOffset.current) > swipeThreshold;

        if (tapped || !reachedThreshold) return;

        if (position < dragOffset.current) setPrevious();
        else setNext();

        dragOffset.current = 0;
    };

    const setPrevious = () => {
        const getPreviousDevice = device => (device === 0 ? laptops.length - 1 : device - 1);
        const previous = getPreviousDevice(current);

        if (previous !== currentComparison) setCurrentDevice(previous);
        else setCurrentDevice(getPreviousDevice(previous));
        
        CSSService.setCustomProperty("revealOffset", `${-revealOffset}px`, laptopShowcaseRef.current);
        currentOffset.current = -revealOffset;
    };

    const setNext = () => {
        const getNextDevice = device => (device + 1) % laptops.length;
        const next = getNextDevice(current);

        if (next !== currentComparison) setCurrentDevice(next);
        else setCurrentDevice(getNextDevice(next));
        
        CSSService.setCustomProperty("revealOffset", `${revealOffset}px`, laptopShowcaseRef.current);
        currentOffset.current = revealOffset;
    };

    const setDisclaimers = () => {
        let disclaimers = [];

        laptops[current].specs.forEach(({ disclaimer }) => {
            const isArray = typeof disclaimer === "object";
            if (disclaimer) disclaimers.push(isArray ? disclaimer.join("\n") : disclaimer);
        });

        return disclaimers;
    };

    const disableInteractions = (overflow, pointerEvents) => {
        CSSService.setCustomProperty("animWrapperOverflowY", overflow);
        document.body.style.pointerEvents = pointerEvents;
    }

    const disclaimers = setDisclaimers();

    return (
        <div
            ref={laptopShowcaseRef}
            className={styles.laptopShowcase}
            style={{ "--revealTimeout": `${revealTimeout}ms` }}
        >
            <div
                className={styles.sliderWrapper}
                onPointerDown={handlePointerDown}
                onPointerUp={handlePointerUp}
                onPointerLeave={handlePointerUp}
            >
                <button onClick={setPrevious}>
                    <i className={`ms-Icon ms-Icon--${isDesktop ? "ChevronUp" : "ChevronLeft"}`} />
                </button>

                <SwitchTransition mode="out-in">
                    <CSSTransition
                        key={current}
                        timeout={revealTimeout}
                        onEnter={() => CSSService.setCustomProperty("revealOffset", `${-currentOffset.current}px`, laptopShowcaseRef.current)}
                        onEntered={() => disableInteractions("auto", "all")}
                        onExiting={() => disableInteractions("hidden", "none")}
                        classNames={{
                            enter: styles.laptopEnter,
                            enterActive: styles.laptopEnterActive,
                            exit: styles.laptopExit,
                            exitActive: styles.laptopExitActive
                        }}
                    >
                        <Laptop laptop={laptops[current]} {...{ tableVisible }} />
                    </CSSTransition>
                </SwitchTransition>

                <button onClick={setNext}>
                    <i className={`ms-Icon ms-Icon--${isDesktop ? "ChevronDown" : "ChevronRight"}`} />
                </button>
            </div>

            {showDisclaimers && (
                <Disclaimer
                    disclaimers={disclaimers}
                    customStyles={disclaimersToRight ? styles.disclaimersToRight : ""}
                    isScrollable={true}
                />
            )}
        </div>
    );
};

LaptopShowcase.propTypes = {
    laptops: PropTypes.array.isRequired,
    current: PropTypes.number.isRequired,
    currentComparison: PropTypes.number.isRequired,
    disclaimersToRight: PropTypes.bool,
    setCurrentDevice: PropTypes.func.isRequired,
    showDisclaimers: PropTypes.bool.isRequired,
    tableVisible: PropTypes.bool
};

export default LaptopShowcase;
